<template>
    <li class="nav-item dropdown header-nav-dropdown" :class="!parseInt(inbox) ? has_notified:''">
		<a href="#" @click.prevent="getMessage" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" class="nav-link">
			<span v-if="parseInt(inbox)" class="oi oi-envelope-open"></span>
			<span v-else class="oi oi-envelope-closed"></span>
		</a>
		<div class="dropdown-menu dropdown-menu-rich dropdown-menu-right" :class="{'show' : awake}" @click="stayAwake">
			<div class="dropdown-arrow"></div>
			<h6 class="dropdown-header stop-propagation">
				<span>Messages <span class="badge">({{ unreadMessage }})</span></span>
				<message-real-all
					:messageCount.sync="unreadMessage"
					:hasNotified.sync="has_notified"
					:allMessages.sync="messages"
					:url_="url"
					:loading_.sync="loading"
					classes=""
					title_="Mark all as read"
				></message-real-all>
			</h6>
			<div class="dropdown-scroll stop-propagation perfect-scrollbar ps ps--active-y" style="height: unset !important;" :style="">
				<div v-if="loading">
					<a href="#" class="">
						<div class="">
							<p class="text text-truncate text-center py-3 mb-0"> <i class="fad fa-spinner fa-pulse fa-2x"></i> </p>
						</div>
					</a>
				</div>
				<div v-else>
					<div v-if="messages.length > 0">
						<a href="#" v-for="(message,index) in messages" @click.prevent="viewMessage(message, index, parseInt(message.sender_id) > 0 ? message.sender_name:locale.boinaw_team, diffForHumans(message.created_at), base+'/'+message.admin_photo, parseInt(message.sender_id) > 0)" class="dropdown-item py-2" :class="{unread: message.status == 1}">

							<div v-if="parseInt(message.sender_id) > 0" class="user-avatar"><img :src="base+'/'+message.admin_photo" alt=""></div>
							<div v-else><a class="tile tile-circle bg-primary font-niconne font-weight-normal">BN</a> <span class=""></span></div>

							<div class="dropdown-item-body">
								<p class="subject"> {{ message.admin_name }} </p>
								<p class="text text-truncate"> {{ message.message_subject }} </p>
								<span class="date">{{ diffForHumans(message.created_at) }}</span>
							</div>
						</a>
					</div>
					<div v-else>
						<div class="alert alert-warning has-icon mb-0 rounded-0" role="alert">
							<div class="alert-icon">
								<span class="oi oi-info"></span>
							</div>No message available.
						</div>
					</div>
				</div>
			</div>
			<a :href="url_all_message" class="dropdown-footer">All messages <i class="fas fa-fw fa-long-arrow-alt-right"></i></a>
		</div>
	</li>
</template>
<script>
	import { store } from '../../store'
	import { isLoggedMixin } from '../../myMixins'
	export default {
		name: 'NavMessage',
		mixins:[isLoggedMixin],
		props: ['url', 'url_all_message', 'url_this_read', 'inbox'],
		data(){
			return {
				base: '',
				locale: {},
				has_notified: '',
				unreadMessage: 0,
				messages: {},
				has_data: true,
				loading: true,
				open: false,
				awake: false
			}
		},
		mounted(){
			this.base = store.state.my_url;
			this.locale = store.state.file_default;

			this.getMessage(false);
		},
		methods: {
			viewMessage: function (thisMessage, index, sender, time, photo_url, photo) {

				if (thisMessage.status == 1) {
					let status_x = 2;
					axios.get(this.url_this_read+'/'+encode_base64(thisMessage.id)+'/'+encode_base64(status_x), {
						params: {},
					}).then((response) => {
						if (response.data == 1) {
							this.messages[index].status = status_x;
							if (this.unreadMessage > 0) this.unreadMessage -= 1;
						} else {
							toastr.error(this.locale.something_went_wrong+'!')
						}
					}).catch((error) => {
						console.error(error);
						toastr.error(this.locale.something_went_wrong+'!')
					});
				}

				let message_body = thisMessage.message_body;
				let message_subject = thisMessage.message_subject;
				let messageTitle = '';
				if (photo) {
					messageTitle = 	`<div class="list-group-item p-0">`+
										`<div class="list-group-item-figure pr-1 pl-0">`+
											`<div class="avatar has-badge">`+
												`<a href="#" class="user-avatar"><img src="${photo_url}" alt=""></a> <span class=""></span>`+
											`</div>`+
										`</div>`+
										`<div class="list-group-item-body pl-md-2">`+
											`<div class="row">`+
												`<div class="col-12">`+
													`<h4 class="list-group-item-title text-truncate">`+
														`<a href="#" class="cursor-default">${sender}</a>`+
													`</h4>`+
													`<p class="list-group-item-text small"> ${time} </p>`+
												`</div>`+
											`</div>`+
										`</div>`+
									`</div>`;
				}
				else {
					messageTitle = 	`<div class="list-group-item p-0">`+
										`<div class="list-group-item-figure pr-1 pl-0">`+
											`<div class="avatar has-badge">`+
												`<a href="#" @click.prevent="" class="tile tile-circle bg-primary font-niconne font-weight-normal">BN</a> <span class=""></span>`+
											`</div>`+
										`</div>`+
										`<div class="list-group-item-body pl-md-2">`+
											`<div class="row">`+
												`<div class="col-12">`+
													`<h4 class="list-group-item-title text-truncate">`+
														`<a href="#" class="cursor-default">${sender}</a>`+
													`</h4>`+
													`<p class="list-group-item-text small"> ${time} </p>`+
												`</div>`+
											`</div>`+
										`</div>`+
									`</div>`;
				}
				const bvHtml = this.$createElement;
				const message = bvHtml('div', { domProps: { innerHTML: '<h4 class="list-group-item-title mb-3 font-weight-bold"><a href="#" class="cursor-default">'+message_subject+'</a></h4><p class="list-group-item-text">'+message_body+'</p>' } });
				const title = bvHtml('div', { domProps: { innerHTML: messageTitle } });

				this.$bvModal.msgBoxOk([message], {
					title: [title],
					// size: 'sm',
					buttonSize: 'sm',
					okVariant: 'success',
					modalClass: 'stop-propagation',
					okTitle: this.locale.ok,
					// cancelTitle: this.locale.no,
					bodyClass: 'mb-3',
					footerClass: 'd-none p-2',
					hideHeaderClose: false,
					centered: true
				})
				.then(value => {
					console.log(value);
				})
				.catch(err => {
					console.log(err);
				})
			},
			stayAwake: function () {
				this.awake = true;
			},
			getMessage: function (checkOpen=true) {
				this.loading = true;
				if (checkOpen){
					this.open = !this.open;
				}

				if(this.open || !checkOpen)
				{
					axios.get(this.url, {
						params: {
							ref: encode_base64(_.random()),
						},
					}).then((response) => {
						this.loading = false;
						this.messages = response.data[0].data;
						this.unreadMessage = response.data[1];
						if (parseInt(response.data[1]) > 0) {
							this.has_notified = 'has-notified'
						}
					}).catch((error) => {
						this.loading = false;
						console.error(error);
					});
				} else {
					this.loading = false;
				}

				this.open = false;
			},
			diffForHumans: (date) => {
				return diffForHumans(date);
			},
		}
	};
</script>