<template>
	<div>

		<div class="card card-fluid radius-0 border-primary box-shadow">
			<div class="card-header border-0 bg-primary text-white rounded-0 py-2 justify-content-between align-items-center d-flex">
				<a class="card-title mb-0"> {{ this_book.title }} </a>
				<i v-if="this_book.taken_by_id > 0" class="fa fa-check-circle"></i>
			</div>
			<div v-if="this_history.length > 0" class="card-body p-0 py-2">
				<div class="location-container">
					<div v-for="(item, index) in this_history" class="col-12 col-sm-6 col-md-4 col-lg-3 my-auto px-1 pb-1" :key="item.id">
						<div class="media list-group-item justify-content-between align-items-center border-0 text-decoration-none px-0 cursor-pointer pl-1 dotted-x" :class="[is_taken(this_book.taken_by_id, item.admin_id), this_history[index].active ? 'bg-muted':'']" @click="acceptBellConfirm(accept_confirm_url, this_book.id, item.admin_id, item.name, index)">
							<a href="#" @click.prevent class="user-avatar user-avatar-md mr-1"><img :src="base+'/'+photo(item.photo)" alt=""></a>
							<div class="media-body">
								<h3 class="card-title">
									<a href="#" @click.prevent>{{ item.name }}</a>
								</h3>
								<h6 class="card-subtitle text-muted"> {{ diffForHumans(item.created_at) }} </h6>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div v-else class="card-body p-0">
				<div class="d-sm-flex justify-content-sm-between align-items-sm-center">
					<div class="team w-100">
						<div class="alert alert-warning has-icon mb-0 rounded-0" role="alert">
							<div class="alert-icon">
								<span class="oi oi-info"></span>
							</div>
							<span class="d-block mt-1"><strong>{{ lang.no_one_belled_yet }}</strong></span>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import { store } from '../../store';

	export default {
		name: 'BellList',
		props: ['book', 'accept_history', 'texts', 'accept_confirm_url'],
		data(){
			return {
				this_book: JSON.parse(this.book),
				this_history: JSON.parse(this.accept_history),
				this_texts: JSON.parse(this.texts),
				count_texts: JSON.parse(this.texts).length,
				base: '',
				lang: '',
				locale: '',
				this_admin_id: '',
				taken_by_id: '',
		        boxOne: '',
		        boxTwo: '',
		        taken_class: ''
			}
		},
		mounted(){
			this.this_admin_id = decode_base64(store.state.auth_id);
			this.locale = store.state.locale;
			this.base = store.state.my_url;
			this.lang = store.state.file_default;
		},
		methods: {
			acceptBellConfirm: function (url, base64_id, base64_admin_id, name, index) {
				this.boxTwo = '';
				const bvHtml = this.$createElement;

				const message = bvHtml('div', { domProps: { innerHTML: this.lang.we_ll_notify+' <strong>'+name+'</strong> '+this.lang.that_you_ve_chosen_him_for_this_book+this.lang['.'] } });
				const title = this.$createElement(
					'span',
					{ class: ['text-info'] },
					this.lang.are_you_sure+'?'
				);
				if (!(this.this_book.taken_by_id > 0)) {
					this.$bvModal.msgBoxConfirm([message], {
						title: [title],
						// size: 'sm',
						buttonSize: 'sm',
						okVariant: 'success',
						okTitle: this.lang.yes,
						cancelTitle: this.lang.no,
						footerClass: 'p-2',
						hideHeaderClose: false,
						centered: true
					})
					.then(value => {
						this.boxTwo = value
						if (value) {

							axios.get(url+'/'+encode_base64(base64_id)+'/'+encode_base64(base64_admin_id))
							.then((response) => {
								console.log(response.data);
								if (response.data == 1) {
									toastr.success(this.lang.success+'!');

									this.taken_class = 'bg-muted';
									this.this_book.taken_by_id = base64_admin_id;
									let item = this.this_history[index];
									item.active = true;
								}
								else {
									toastr.error(this.lang.access_unauthorized+'!');

									this.this_book.taken_by_id = 0;
									this.taken_class = '';
								}
							})
							.catch((exception) => {
								toastr.error(this.lang.something_went_wrong+'!');
								console.log(exception);

								this.this_book.taken_by_id = 0;
								this.taken_class = '';
							});
						}
					})
					.catch(err => {
						console.log(err);
						toastr.danger(this.lang.something_went_wrong+'!');
					})
				}
				else {
					toastr.warning(this.lang.already_given+this.lang['.']);
				}
			},
			// acceptBellConfirm: function (url, base64_id, base64_admin_id, name) {
			// 	if (!(this.this_book.taken_by_id > 0)) {
			// 		acceptBellConfirm(url, encode_base64(base64_id), encode_base64(base64_admin_id), name);
			// 	} else {
			// 		toastr.warning(this.lang.already_given+this.lang['.']);
			// 	}
			// },

			photo: (url) => { return url != null ? url:'public/images/default.png'; },

			getColor: function () {
				return this.this_texts[_.random(56789, 98765)%(this.count_texts-1)];
			},

			diffForHumans: (date) => {
				return diffForHumans(date);
			},

			is_taken: function (taken_by_id, admin_id) {
				if (this.this_book.taken_by_id > 0) {
					if (taken_by_id == admin_id) {
						return 'bg-muted';
					} else {
						return '';
					}
				} else {
					return 'bg-muted-h';
				}
			},
		},
		created(){

		}
	};
</script>