<template>
	<a href="#" @click.prevent="markRead" :class="classes">{{ title_ }}</a>
</template>
<script>
	import { store } from '../../store'
	export default {
		name: 'MessageReadAll',
		props: ['messageCount', 'hasNotified', 'allMessages', 'url_', 'loading_', 'classes', 'title_'],
		data(){
			return {
				base: '',
				local: '',
				messages: {},
			}
		},
		mounted(){
			this.base = store.state.my_url;
			this.locale = store.state.file_default;
		},
		methods: {
			markRead: function () {
				this.$emit('update:loading_', true);

				axios.get(this.url_, {
					params: {
						ref: encode_base64(_.random()),
						readAll: 1,
					},
				}).then((response) => {
					if (response.data) {
						this.$emit('update:loading_', false);
						this.$emit('update:allMessages', response.data[0].data);
						this.$emit('update:messageCount', 0);
						if (parseInt(response.data[1]) > 0) {
							this.$emit('update:hasNotified', 'has-notified');
						}

					  	toastr.success(this.locale.success+'!');
					}
				  	else {
			  			toastr.error(this.locale.something_went_wrong+'!');
			  		}
				}).catch((error) => {
					this.$emit('update:loading_', false);
			  		toastr.error(this.locale.something_went_wrong+'!');
					console.error(error);
				});
			}
		}
	};
</script>