<template>
	<div class="row">
		<div class="col-md-5 order-md-2">
			<div v-if="!is_empty(notification)">
				<div class="card box-shadow mb-2 rounded-0">
					<div class="card-header p-0">
						<div class="list-group-item">
							<!-- notification figure -->
							<div class="list-group-item-figure pr-1">
								<div class="avatar has-badge">
									<div v-if="parseInt(notification.sender_id) > 0">
										<a href="#" @click.prevent="" class="user-avatar"><img :src="base+'/'+notification.admin_photo" alt=""></a> <span class=""></span>
									</div>
									<div v-else>
										<a href="#" @click.prevent="" class="tile tile-circle bg-primary font-niconne font-weight-normal">BN</a> <span class=""></span>
									</div>
								</div><!-- /.avatar -->
							</div><!-- /notification figure -->
							<!-- notification body -->
							<div class="list-group-item-body pl-md-2">
								<!-- grid row -->
								<div class="row">
									<!-- grid column -->
									<div class="col-12">
										<h4 class="list-group-item-title text-truncate">
											<a href="#" @click.prevent="" class="cursor-default">{{ parseInt(notification.sender_id) > 0 ? notification.sender_name:locale.boinaw_team }}</a>
										</h4>
										<p class="list-group-item-text small"> {{ diffForHumans(notification.created_at) }} </p>
									</div><!-- /grid column -->
								</div><!-- /grid row -->
							</div><!-- /notification body -->
							<!-- notification actions -->
							<div class="list-group-item-figure">
								<!-- .dropdown -->
								<div class="dropdown">
									<button class="btn btn-sm btn-icon btn-secondary" data-toggle="dropdown"><i class="fad fa-ellipsis-h"></i></button> <!-- .dropdown-menu -->
									<div class="dropdown-menu dropdown-menu-right">
										<div class="dropdown-arrow mr-n1"></div>
										<a href="#" @click.prevent="mark_this_read(notification, thisIndex)" class="dropdown-item" :class="notification.status == 2 ? 'text-muted bg-muted-h cursor-default':''">Mark as read</a>
										<!-- <notification-delete-this
											:notificationCount.sync="unreadNotification"
											:hasNotified.sync="has_notified"
											:allNotifications.sync="notifications"
											:thisNotification.sync="notification"
											:currentNotification.sync="notification"
											:url_="url_delete_this"
											:loading_.sync="loading"
											classes="dropdown-item"
											title_="Trash"
										></notification-delete-this> -->
									</div><!-- /.dropdown-menu -->
								</div><!-- /.dropdown -->
							</div><!-- /notification actions -->
						</div>
					</div>
					<div class="card-body">
						<h4 class="list-group-item-title mb-3">
							<span v-html="notification.message_subject"></span>
						</h4>
						<p class="list-group-item-text"> <span v-html="notification.message_body"></span> </p>
					</div>
					<div class="card-footer"></div>
				</div>
			</div>
			<div v-else>
				<div class="text-muted">
					<div class="log-divider">
						<span class="bg-light"><i class="fad fa-fw fa-bell fa-2x"></i></span>
					</div>
				</div>
			</div>
		</div>
		<div class="col-md-4">
			<!-- .page-title-bar -->
			<header class="page-title-bar mb-2 px-0">
				<div class="d-flex justify-content-between">
					<div class="d-md-inline-block w-100">
						<!-- .input-group -->
						<div class="input-group input-group-alt">
							<!-- .input-group -->
							<div class="input-group rounded-0">
								<div class="input-group-prepend">
									<span class="input-group-text"><span class="fad fa-search"></span></span>
								</div>
								<input type="text" class="form-control pr-1" @input="getSearchResult" v-model="model_search" placeholder="Searching here...">
								<div v-if="searching" class="my-auto pr-1">
									<i class="fad fa-spinner fa-pulse"></i>	
								</div>
							</div><!-- /.input-group -->
							<!-- .input-group-append -->
							<div class="input-group-append">
								<div role="group" class="btn-group">
									<button id="btnGroupDrop2" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" class="btn btn-secondary"><i class="fad fa-ellipsis-v"></i></button>
									<div aria-labelledby="btnGroupDrop2" class="dropdown-menu dropdown-menu-right" style="">
										<div class="dropdown-arrow"></div>
										<notification-real-all
											:notificationCount.sync="unreadNotification"
											:hasNotified.sync="has_notified"
											:allNotifications.sync="notifications"
											:url_="url_read_all"
											:loading_.sync="loading"
											classes="dropdown-item"
											title_="Read All"
										></notification-real-all>
									</div>
								</div>
							</div><!-- /.input-group-append -->
						</div><!-- /.input-group -->
					</div>
				</div>
				<button type="button" class="btn btn-success btn-floated d-none d-sm-none">
					<span class="fa fa-plus"></span>
				</button>
			</header><!-- /.page-title-bar -->
			<!-- .page-section -->
			<div v-if="!is_empty(notifications)">
				<div class="page-section">
					<!-- .card -->
					<div class="card rounded-0">
						<!-- .list-group-notifications -->
						<div class="list-group list-group-messages list-group-flush list-group-bordered py-2 sidebar">
							<div v-for="(item, index) in notifications" class="my-1px sidebar-item" :class="[parseInt(item.status)==1 ? 'unread':'', item.active ? 'bg-secondary active':'']">
								<!-- notification item -->
								<div class="list-group-item pt-2 pb-0 notification-side-item">
									<!-- notification figure -->
									<div class="list-group-item-figure pr-1" @click.prevent="view_notification(item, index)">
										<!-- .avatar -->
										<div class="avatar has-badge">
											<div v-if="parseInt(item.sender_id) > 0">
												<a href="#" @click.prevent="" class="user-avatar"><img :src="base+'/'+item.admin_photo" alt=""></a> <span class=""></span>
											</div>
											<div v-else>
												<a href="#" @click.prevent="" class="tile tile-circle bg-primary font-niconne font-weight-normal">BN</a> <span class=""></span>
											</div>
										</div><!-- /.avatar -->
									</div><!-- /notification figure -->
									<!-- notification body -->
									<div class="list-group-item-body pl-md-2 cursor-pointer" @click.prevent="view_notification(item, index)">
										<!-- grid row -->
										<div class="row">
											<!-- grid column -->
											<div class="col-12">
												<h4 class="list-group-item-title text-truncate">
													<a href="#" @click.prevent="">{{ item.sender_id > 0 ? item.sender_name:locale.boinaw_team }}</a>
													<!-- <a href="#" @click.prevent="">{{ item.sender_name }}</a> -->
												</h4>
												<h4 class="list-group-item-title text-truncate">
													<a href="#" @click.prevent="" class="small text-muted"> <span v-html="item.message_subject"></span> </a>
												</h4>
											</div><!-- /grid column -->
											<!-- grid column -->
											<div class="col-12">
												<p class="list-group-item-text small"> {{ diffForHumans(item.created_at) }} </p>
											</div><!-- /grid column -->
										</div><!-- /grid row -->
									</div><!-- /notification body -->
									<!-- notification actions -->
									<div class="list-group-item-figure" @click.prevent="">
										<!-- .dropdown -->
										<div class="dropdown">
											<button class="btn btn-sm btn-icon btn-secondary" data-toggle="dropdown"><i class="fad fa-ellipsis-h"></i></button> <!-- .dropdown-menu -->
											<div class="dropdown-menu dropdown-menu-right">
												<div class="dropdown-arrow mr-n1"></div>
												<a href="#" @click.prevent="mark_this_read(item, index)" class="dropdown-item" :class="item.status == 2 ? 'text-muted bg-muted-h cursor-default':''">Mark as read</a>
												<!-- <notification-delete-this
													:notificationCount.sync="unreadNotification"
													:hasNotified.sync="has_notified"
													:allNotifications.sync="notifications"
													:thisNotification.sync="item"
													:currentNotification.sync="notification"
													:url_="url_delete_this"
													:loading_.sync="loading"
													classes="dropdown-item"
													title_="Trash"
												></notification-delete-this> -->
											</div><!-- /.dropdown-menu -->
										</div><!-- /.dropdown -->
									</div><!-- /notification actions -->
								</div><!-- /notification item -->
							</div>

						</div><!-- /.list-group-notifications -->
					</div><!-- /.card -->
					<!-- .section-block -->
					<div class="section-block">
						<!-- .pagination -->
						<nav v-if="pagination.data.length > 0 && pagination.last_page != 1" aria-label="...">
							<ul class="pagination pagination-sm justify-content-center">
								<li class="page-item cursor-pointer" :class="{ disabled: pagination.current_page <= 1 }">
									<a class="page-link" @click.prevent="bodyNotification(pagination.current_page - 1)">{{ locale.previous }}</a>
								</li>
								<li class="page-item cursor-pointer" :class="isCurrentPage(1) ? 'active' : ''">
									<a class="page-link" @click.prevent="bodyNotification(1)"  >{{ _n2l('1') }}
										<span v-if="isCurrentPage(1)" class="sr-only">(current)</span>
									</a>
								</li>


								<li v-if="leftDots_" class="page-item cursor-default disabled">
									<a class="page-link" @click.prevent="">...</a>
								</li>
								<li class="page-item cursor-pointer" v-for="page in pages" :key="page" :class="isCurrentPage(page) ? 'active' : ''">
									<a class="page-link" @click.prevent="bodyNotification(page)">{{ _n2l(page) }}
										<span v-if="isCurrentPage(page)" class="sr-only">(current)</span>
									</a>
								</li>
								<li v-if="rightDots_" class="page-item cursor-default disabled">
									<a class="page-link" @click.prevent="">...</a>
								</li>


								<li class="page-item cursor-pointer" :class="isCurrentPage(pagination.last_page) ? 'active' : ''">
									<a class="page-link" @click.prevent="bodyNotification(pagination.last_page)">{{ _n2l(pagination.last_page) }}
										<span v-if="isCurrentPage(pagination.last_page)" class="sr-only">(current)</span>
									</a>
								</li>
								<li class="page-item cursor-pointer" :class="{ disabled: pagination.current_page >= pagination.last_page }">
									<a class="page-link" @click.prevent="bodyNotification(pagination.current_page + 1)">{{ locale.next }}</a>
								</li>
							</ul>
						</nav><!-- /.pagination -->
					</div><!-- /.section-block -->
				</div><!-- /.page-section -->
			</div>
			<div v-else>
				<div v-if="init_loading">
					<div class="row">
						<div class="col-12 py-4 text-center">
							<i class="fad fa-spinner fa-pulse text-primary fa-2x"></i>
						</div>
					</div>
				</div>
				<div v-else>
					<div class="row">
						<div class="col-12">
							<div class="alert alert-warning has-icon mb-0 rounded-0" role="alert">
								<div class="alert-icon">
									<span class="oi oi-info"></span>
								</div>No notification available.
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import { store } from '../../store'
import _ from 'lodash'
import { isLoggedMixin } from '../../myMixins'
export default {
	name: 'NotificationCard',
	mixins:[isLoggedMixin],
	props: ['url', 'url_read_all', 'url_this_read', 'url_delete_this', 'url_search'],
	data(){
		return {
			base: '',
			locale: {},
			has_notified: '',
			unreadNotification: 0,
			notifications: {},
			notification: {},
			view_container: false,
			has_data: true,
			loading: true,
			open: false,
			awake: false,
			init_loading: true,

			model_search: '',
			on_search: false,
			searching: false,

			pagination: {},
			offset: 3,
			leftDots_: false,
			rightDots_: false,
			thisIndex: 0,
		}
	},
	mounted(){
		this.base = store.state.my_url;
		this.locale = store.state.file_default;

		this.initNotification();
	},
	methods: {
		diffForHumans: (date) => {
			return diffForHumans(date);
		},
		_n2l: function (str) {
			return n2l(str.toString());
		},
		_n2l_int: function (str) {
			return isNaN(parseInt(str)) ? n2l('0'):n2l(parseInt(str).toString());
		},
		mark_this_read: function (thisNotification, thisIndex) {
			if (thisNotification.status == 1) {
				let status_x = 2;
				axios.get(this.url_this_read+'/'+encode_base64(thisNotification.id)+'/'+encode_base64(status_x), {
				  params: {},
				}).then((response) => {
					if (response.data == 1) {
						this.notifications[thisIndex].status = status_x;
					} else {
				  		toastr.error(this.locale.something_went_wrong+'!')
					}
				}).catch((error) => {
				  console.log(error);
				  toastr.error(this.locale.something_went_wrong+'!')
				});
			}
		},
		getSearchResult: _.debounce(function () {
			this.searching = true;
			if(this.model_search.length > 0){
				this.notification = '';

				axios.post(this.url_search, {
					ref: encode_base64(_.random()),
					q: this.model_search,
					notificationCard: true,
				}).then((response) => {
					let index = 0;
					this.searching = false;
					this.init_loading = false;
					this.on_search = true;
					this.pagination = response.data[0];
					this.notifications = response.data[0].data;
					this.changePage(this.pagination.current_page);

	              	if (parseInt(response.data[1]) > 0) {
	              		this.has_notified = 'has-notified';
	              	}

              		window.scrollTo({ top: 0, behavior: 'smooth' });
              	}).catch((error) => {
              		this.init_loading = false;
					this.searching = false;

              		console.error(error);
              		toastr.error(this.locale.something_went_wrong+'!')
              	});
            }
            else {
				this.searching = false;
				this.on_search = false;
              	this.initNotification();
            }
			// console.log(this.notifications)
		}, 1000),
		initNotification: function () {
			axios.get(this.url, {
				params: {
					ref: encode_base64(_.random()),
					notificationCard: true
				},
			}).then((response) => {
				this.init_loading = false;

				let index = 0;
				this.pagination = response.data[0];
				this.notifications = response.data[0].data;
				// this.notification = this.notifications[index];
              	this.changePage(this.pagination.current_page);
              	// this.view_notification(this.notification, index);

				if (parseInt(response.data[1]) > 0) {
					this.has_notified = 'has-notified';
				}

				window.scrollTo({ top: 0, behavior: 'smooth' });
			}).catch((error) => {
				this.init_loading = false;

				console.error(error);
				toastr.error(this.locale.something_went_wrong+'!')
			});
		},
		bodyNotification: function (p=0) {
			let page = 1; if (p > 0) page = p;

			// let new_url = '';
          	// window.history.pushState({}, null, this.new_url);
          	if (!this.on_search) {
				axios.get(this.url, {
					params: {
						page: page,
						ref: encode_base64(_.random()),
						notificationCard: true
					},
				}).then((response) => {
					let index = 0;
					this.pagination = response.data[0];
					this.notifications = response.data[0].data;

					let notification_ = this.notification;
					let notifications_ = this.notifications;

					Object.keys(this.notifications).forEach( function(element, ind) {
						if(notifications_[element].id == notification_.id) {	
							notifications_[element].active = true;
						}
					});

	              	this.changePage(this.pagination.current_page);
	              	// this.view_notification(this.notification, index);
	              	 
					window.scrollTo({ top: 0, behavior: 'smooth' });
				}).catch((error) => {
					console.error(error);
					toastr.error(this.locale.something_went_wrong+'!')
				});
			} else {
				axios.post(this.url_search+'?page='+page, {
					ref: encode_base64(_.random()),
					q: this.model_search,
					notificationCard: true,
				}).then((response) => {
					let index = 0;
					this.pagination = response.data[0];
					this.notifications = response.data[0].data;

					let notification_ = this.notification;
					let notifications_ = this.notifications;

					Object.keys(this.notifications).forEach( function(element, ind) {
						if(notifications_[element].id == notification_.id) {	
							notifications_[element].active = true;
						}
					});

	              	this.changePage(this.pagination.current_page);
	              	// this.view_notification(this.notification, index);
	              	 
					window.scrollTo({ top: 0, behavior: 'smooth' });
				}).catch((error) => {
					console.error(error);
					toastr.error(this.locale.something_went_wrong+'!')
				});

			}

		},
		is_empty: (notification) => {
			return _.isEmpty(notification);
		},
		view_notification: function (notification, index) {
			if(notification) {
				this.view_container = true;
				this.thisIndex = index;
				let notifications = this.notifications;
				Object.keys(this.notifications).forEach( function(element, ind) {
					notifications[element].active = false;
				});

				let item = this.notifications[index];
				item.active = true;

				window.scrollTo({ top: 0, behavior: 'smooth' });
				this.notification = notification;
			}
		},
		isCurrentPage(page){
			return this.pagination.current_page === page
		},
		changePage(page) {

			if (page > this.pagination.last_page) {
				page = this.pagination.last_page;
			}
			this.pagination.current_page = page;

			if (this.offset >= page) this.leftDots_ = false;
			else this.leftDots_ = true;

			if (this.pagination.last_page - this.offset + 1 <= page) this.rightDots_ = false;
			else this.rightDots_ = true;


			if (this.pagination.last_page - 1 == this.offset) this.rightDots_ = false;
		}
	},
	computed: {
		pages() {
			let pages = []
			let from = this.pagination.current_page - Math.floor(this.offset / 2)
			if (from < 1) {
				from = 1
			}
			let to = from + this.offset - 1

			if (to > this.pagination.last_page) {
				to = this.pagination.last_page
			}
			while (from <= to) {
				if (from > 1 && from < this.pagination.last_page) {
					pages.push(from)
				}
				from++
			}

			return pages
		}
	},
};
</script>