<template>

	<li class="nav-item dropdown header-nav-dropdown" :class="has_notified">
		<a href="#" @click.prevent="getNotification" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" class="nav-link"><span class="oi oi-pulse"></span></a> 
		<div class="dropdown-menu dropdown-menu-rich dropdown-menu-right">
			<div class="dropdown-arrow"></div>
			<h6 class="dropdown-header stop-propagation">
				<span>Notifications <span class="badge">({{ unreadNotification }})</span></span>
				<notification-real-all
					:notificationCount.sync="unreadNotification"
					:hasNotified.sync="has_notified"
					:allNotifications.sync="notifications"
					:url_="url"
					:loading_.sync="loading"
					classes=""
					title_="Mark all as read"
				></notification-real-all>
			</h6>
			<div class="dropdown-scroll stop-propagation perfect-scrollbar ps" style="height: unset !important;">

				<div v-if="loading">
					<a href="#" class="">
						<div class="">
							<p class="text text-truncate text-center py-3 mb-0"> <i class="fad fa-spinner fa-pulse fa-2x"></i> </p>
						</div>
					</a>
				</div>
				<div v-else>
					<div v-for="notification in notifications" class="dropdown-item py-2 each-notification" :class="{'unread': notification.status == '1'}">

						<div v-if="parseInt(notification.sender_id) > 0" class="user-avatar"><img :src="base+'/'+notification.admin_photo" alt=""></div>
						<div v-else><a class="tile tile-circle bg-primary font-niconne font-weight-normal">BN</a> <span class=""></span></div>

						<div class="dropdown-item-body">
							<p class="text"> <span v-html="notification.message_subject"></span> </p>
							<span class="date">{{ diffForHumans(notification.created_at) }}</span>
						</div>
					</div>
				</div>
			</div>
			<a :href="url_all_notification" class="dropdown-footer stop-propagation">All Notifications <i class="fad fa-fw fa-long-arrow-alt-right"></i></a>
		</div>
	</li>
</template>
<script>
	import { store } from '../../store'

	export default {
		name: 'NavNotifications',
		props: ['url', 'url_all_notification'],
		data(){
			return {
				base: '',
				locale: {},
				notifications: {},
				has_notified: '',
				unreadNotification: 0,

				loading: false,
			}
		},
		mounted(){
			this.base = store.state.my_url;
			this.locale = store.state.file_default;
			this.getNotification();
		},
		methods: {
			diffForHumans: (date) => {
				return diffForHumans(date);
			},
			_n2l: function (str) {
				return n2l(str.toString());
			},
			_n2l_int: function (str) {
				return isNaN(parseInt(str)) ? n2l('0'):n2l(parseInt(str).toString());
			},
			getNotification: function() {
				this.loading = true;
				axios.get(this.url, {
				}).then((response) => {
					this.loading = false;
					this.notifications = response.data[0].data;
					this.unreadNotification = response.data[1];
					if (parseInt(response.data[1]) > 0) {
						this.has_notified = 'has-notified'
					}
				}).catch((error) => {
					this.loading = false;
				  	console.error(error);
				});
			}
		}
	};
</script>
<style>
	.each-notification{cursor: default;}
	.each-notification p{line-height: 14px;}
	.each-notification a:hover,
	.each-notification:focus a,
	.each-notification:active a,
	.each-notification:hover a {
		color: #fff;
		cursor: pointer;
	}
</style>