<template>
	<div>
    <div class="card rounded-0 border-primary box-shadow mb-0">
      <!-- .card-header -->
      <div class="card-header border-0 bg-primary text-white rounded-0 py-2 d-none">
        <a class="card-title mb-0"> {{ lang.write_your_wish }} </a>
      </div><!-- /.card-header -->
      <!-- .card-body -->
      <div class="card-body">
        <ValidationObserver v-slot="{ invalid }">
          <!-- form .needs-validation -->
          <form method="POST" @submit.prevent="submit($event, invalid)" class="needs-validation" enctype="multipart/form-data">

            <!-- error message -->
            <div v-if="this_errors" class="alert alert-danger px-0 alert-dismissible fade show" role="alert">
              <ul class="list-group border-0 box-shadow-none">
                <li class="list-group-item bg-transparent" v-for="error in this_errors">{{ _n2l(error) }}</li>
              </ul>
              <button type="button" class="close" data-dismiss="alert" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div><!-- /error message -->

            <slot></slot>
            <!-- .form-row -->
            <div class="form-row">
              <!-- form grid -->
              <div class="col-md-6 mb-3">
                <label for="validationTitle">{{ lang.title }} <abbr :title="lang.required">*</abbr></label>
                <ValidationProvider rules="required|min:4" v-slot="{ errors }">
                  <input type="text" name="title" v-model="model_title" class="form-control" :class="errors.length > 0 ? 'is-invalid':''" id="validationTitle" :placeholder="lang.book_title" required="" minlength="4" />
                  <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                </ValidationProvider>
              </div><!-- /form grid -->
              <!-- form grid -->
              <div class="col-md-6 mb-3">
                <div class="form-row">
                  <!-- form grid -->
                  <div class="col-6">
                    <label for="validationEdition">{{ lang.edition }} <abbr :title="lang.required">*</abbr></label>
                    <ValidationProvider rules="required" v-slot="{ errors }">
                      <input type="text" name="edition" v-model="model_edition" class="form-control" :class="errors.length > 0 ? 'is-invalid':''" id="validationEdition" :placeholder="lang.book_edition" required="">
                      <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div><!-- /form grid -->
                  <!-- form grid -->
                  <div class="col-6">
                    <label for="validationGrade">{{ lang.grade }} <abbr :title="lang.required">*</abbr></label>
                    <ValidationProvider rules="required" v-slot="{ errors }">
                      <select name="grade_id" v-model="model_grade_id" class="custom-select d-block w-100" :class="errors.length > 0 ? 'is-invalid':''" id="validationGrade" required="">
                      	<option disabled="" selected=""> {{ lang.choose }}... </option>
                      	<option v-for="grade in all_grades" :value="grade.id"> {{ locale == 'bn' ? grade.title_bn : grade.title }} </option>
                      </select>
                      <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div><!-- /form grid -->
                </div><!-- /form grid -->
              </div><!-- /form grid -->
              <!-- form grid -->
            </div><!-- /.form-row -->
            <div class="form-row">
              <div class="col-md-6 mb-3">
                <label for="validationAuthor">{{ lang.author }} <abbr :title="lang.required">*</abbr></label>
                <ValidationProvider rules="required|min:4" v-slot="{ errors }">
                  <input type="text" name="writer" v-model="model_writer" class="form-control" :class="errors.length > 0 ? 'is-invalid':''" id="validationAuthor" :placeholder="place_author" aria-describedby="inputGroupPrepend" required="" minlength="4">
                  <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                </ValidationProvider>
              </div><!-- /form grid -->
              <!-- form grid -->
              <div class="col-md-6 mb-3">
                <div class="form-row">
                  <div class="col-12">
                    <div class="form-row">
                      <div class="col-6">
                        <label for="validationCost">{{ lang.price }} <abbr :title="lang.required">*</abbr></label>
                        <ValidationProvider rules="positive" v-slot="{ errors }">
                          <input type="number" name="cost" value="0" v-model="model_price" class="form-control" :class="errors.length > 0 ? 'is-invalid':''" id="validationCost" :placeholder="lang.book_price" aria-describedby="inputGroupPrepend" step="1" min="0">
                          <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                        </ValidationProvider>
                      </div>
                      <div class="col-6">
                        <label for="validationMobile">{{ lang.mobile }} <abbr :title="lang.required">*</abbr></label>
                        <ValidationProvider rules="required|min:11" v-slot="{ errors }">
                          <input type="text" name="contact" v-model="model_contact" class="form-control" :class="errors.length > 0 ? 'is-invalid':''" id="validationMobile" :placeholder="lang.mobile_no" aria-describedby="inputGroupPrepend" required="" minlength="11" />
                          <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                        </ValidationProvider>
                      </div>
                    </div>
                  </div>
                </div>
              </div><!-- /form grid -->
            </div><!-- /.form-row -->
            <!-- .form-row -->
            <div class="form-row">
              <!-- grid column -->
              <div class="col-md-6 mb-3">
                <label for="validationDistrict">{{ lang.district }} <abbr :title="lang.required">*</abbr></label>
                <ValidationProvider rules="required" v-slot="{ errors }">
                  <select @change="axiosUpazila" name="district_id" v-model="model_district_id" class="custom-select d-block w-100" :class="errors.length > 0 ? 'is-invalid':''" id="validationDistrict" required="">
                    <option disabled="" selected=""> {{ lang.choose }}... </option>
                    <option v-for="district in all_districts" :value="district.id"> {{ locale == 'bn' ? district.bn_name : district.name }} </option>
                  </select>
                  <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                </ValidationProvider>
              </div><!-- /grid column -->
              <!-- grid column -->
              <div class="col-md-6 mb-3">
                <label class="d-block" for="validationUpazila">{{ lang.upazila }} <abbr :title="lang.required">*</abbr><i v-if="refreshSpin == 'fa-pulse'" class="float-right fad fa-sync-alt" :class="refreshSpin"></i></label>
                <ValidationProvider rules="required" v-slot="{ errors }">
                  <select name="upazila_id" v-model="model_upazila_id" class="custom-select d-block w-100" :class="errors.length > 0 ? 'is-invalid':''" id="validationUpazila" required="">
                    <option disabled="" selected=""> {{ lang.choose }}... </option>
                    <option v-for="upazila in all_upazilas" :value="upazila.id"> {{ locale == 'bn' ? upazila.bn_name : upazila.name }} </option>
                  </select>
                  <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                </ValidationProvider>
              </div><!-- /form grid -->
            </div><!-- /.form-row -->
            <!-- .form-row -->
            <div class="form-row">
              <!-- grid column -->
              <div class="col-md-6">
                <label for="validationAddress">{{ lang.address }}  <abbr :title="lang.required">*</abbr></label>
                <ValidationProvider rules="required|min:4" v-slot="{ errors }">
                  <textarea name="location" v-model="model_address" class="form-control" :class="errors.length > 0 ? 'is-invalid':''" id="validationAddress" rows="3" :placeholder="lang.present_address" aria-describedby="inputGroupPrepend" style="height: 121px;" required="" minlength="4"></textarea>
                  <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                </ValidationProvider>
              </div><!-- /form grid -->
              <!-- grid column -->
              <div class="col-md-6 mb-3">
                <label for="validationDescription">{{ lang.description }}  <abbr :title="lang.required">*</abbr></label>
                <ValidationProvider rules="required|min:4" v-slot="{ errors }">
                  <textarea name="description" v-model="model_description" class="form-control" :class="errors.length > 0 ? 'is-invalid':''" id="validationDescription" rows="3" :placeholder="lang.description" aria-describedby="inputGroupPrepend" style="height: 121px;" required="" minlength="4"></textarea>
                  <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                </ValidationProvider>
              </div><!-- /grid column -->
            </div><!-- /.form-row -->
            <!-- .form-actions -->
            <div class="form-actions">
              <button class="btn btn-primary" type="submit" :disabled="invalid">{{ lang.submit }}</button>
            </div><!-- /.form-actions -->
          </form><!-- /form .needs-validation -->
        </ValidationObserver>
      </div><!-- /.card-body -->
    </div>
	</div>
</template>
<script>
	import { store } from '../../store';

	export default {
		name: 'AddWish',
		props: ['grades', 'districts', 'upazilas', 'upazila_url', 'admin', 'old_inputs', 'form_errors', 'url'],
		data(){
			return {
				all_grades		: JSON.parse(this.grades),
				all_districts	: JSON.parse(this.districts),
				all_upazilas	: JSON.parse(this.upazilas),
				this_admin		: JSON.parse(this.admin),
        this_errors   : JSON.parse(this.form_errors),

				model_contact         : JSON.parse(this.admin).mobile,
				model_grade_id		    : '',
				model_district_id	    : '',
				model_upazila_id      : '',
        model_title           : '',
        model_edition         : '',
        model_writer          : '',
        model_price           : '',
        model_address         : '',
        model_description     : '',

				lang			    : {},
				locale	 		  : '',
				auth_id			  : 0,
				base_url		  : '',
				refreshSpin		: '',
				place_author	: '',
        invalid       : true
			}
		},
		mounted(){
			this.locale        = store.state.locale;
			this.base_url      = store.state.my_url;
			this.admin_id      = store.state.admin_id;
			this.lang          = store.state.file_default;
			this.lang.author_  = { _1: store.state.file_default.author_1, _2: store.state.file_default.author_2, _3: store.state.file_default.author_3};
			this.place_author  = this.lang.author_._1 + '; '+ this.lang.author_._2 + '; '+ this.lang.author_._3 + '; ...';
		},
		methods: {
			diffForHumans: (date) => {
				return diffForHumans(date);
			},
      _n2l: function (str) {
        return _.upperFirst(n2l(str.toString()));
      },
      check_validation: function (error, model) {
        if (error.length>0 && model.length==0) return 'is-invalid'
        else return 'is-valid'
      },
      submit: function(e, is_invalid) {
        if (is_invalid) {
          e.preventDefault()
        } else {
          axios.post(this.url, {
            contact     : this.model_contact,
            grade_id    : this.model_grade_id,
            district_id : this.model_district_id,
            upazila_id  : this.model_upazila_id,
            title       : this.model_title,
            edition     : this.model_edition,
            writer      : this.model_writer,
            cost        : this.model_price,
            location    : this.model_address,
            description : this.model_description

          }).then((response) => {
            console.log(response.data)
            if (response.data == 1) {
              toastr.success(this.lang.success+'!');
            } else if (response.data == 0) {
              toastr.error(this.lang.something_went_wrong+'!');
            } else {
              toastr.warning(this.lang['wished']+this.lang[':']+' '+this.diffForHumans(response.data.created_at));
            }
          }).catch((error) => {
            console.error(error);
            toastr.error(this.lang.something_went_wrong+'!');
          });
        }
      },
			axiosUpazila: function () {
				if (this.model_district_id > 0) {
					this.refreshSpin = 'fa-pulse';

					axios.get(this.upazila_url+'/'+this.model_district_id)
					.then((response) => {
						this.all_upazilas = response.data;
						this.refreshSpin = '';
					})
					.catch((exception) => {
						this.refreshSpin = '';
					    console.log(exception);
					});

				} else {
				}
			},
			axiosPostComment: function () {
				this.loading = 1;
				let data = {
					book_id: this.book_id,
					comment_body: this.comment_body
				};
				if (this.comment_body.length > 0) {
					axios.post(this.post_url, data)
					.then((response) => {
						this.all_comments  = response.data;
						this.comment_body = '';
						this.loading = 0;
						// toastr.success("Comment");
					})
					.catch((exception) => {
						this.loading = 0;
						toastr.error("Something went worng!");
					    console.log(exception);
					});
				} else {
					toastr.warning("Write something in the text area");
					this.loading = 0;
				}
			}
		},
		created(){

		},
    computed: {
    },
    watch: {}
	};
</script>
<style>
	.rounded-0 {border-radius: 0 !important;}
</style>