<template>
  <div>
    <form :action="action" method="GET" @submit.prevent="body_search(val_type)">
      <!-- .card -->
      <div class="card mb-0 radius-0 border-primary">
        <!-- .card-hearder -->
        <div class="card-header list-group-item justify-content-between align-items-center border-0 bg-primary text-white rounded-0">
          <a class="font-weight-bold">{{ lang.search }}</a>
        </div><!-- /.card-hearder -->
        <div class="card-body">
          <!-- .input-group -->
          <div class="input-group input-group-alt mb-0">
            <!-- .input-group-prepend -->
            <div class="input-group-prepend">
              <select class="custom-select rounded-0" @change="body_search(val_type)" name="f" v-model="val_filter">
                <option value="books"> {{ lang.book }} </option>
                <option value="wishes"> {{ lang.wish }} </option>
              </select>
            </div><!-- /.input-group-prepend -->
            <!-- .input-group -->
            <div class="input-group rounded-0">
              <input type="text" name="q" v-model="new_query" class="form-control" :placeholder="lang.search" />
              <div class="input-group-prepend">
                <span class="input-group-text pl-0 pr-2">
                  <i v-if="fa_spin == 'fa-pulse'" class="fad fa-spinner float-right fa-pulse"></i>
                  <i v-else class="fad fa-search"></i>
                </span>
              </div>
            </div><!-- /.input-group -->
          </div><!-- /.input-group -->

          <div class="btn-group btn-group-toggle mt-2 rounded-0" data-toggle="buttons">
            <label class="btn btn-light btn-sm"><input @click="body_search('all'); val_type_set('all')" name="t" value="all" v-model="val_type" type="radio"> {{ lang.all }}</label>
            <label class="btn btn-light btn-sm"><input @click="body_search('new'); val_type_set('new')" name="t" value="new" v-model="val_type" type="radio"> {{ lang.new }}</label>
            <label class="btn btn-light btn-sm"><input @click="body_search('free'); val_type_set('free')" name="t" value="free" v-model="val_type" type="radio"> {{ lang.free }}</label>
          </div>




          <div v-if="search_result.data.length > 0 && val_filter == 'books' " class="card mb-0 card-fluid box-shadow-none border-0">
            <!-- <div class="text-muted px-0"> Showing 1 to {{ search_result.length }} of {{ search_result.length }} results </div> -->
            <!-- .list-group -->
            <div v-for="item in search_result.data" class="list-group list-group-flush list-group-divider">
              <!-- .list-group-item -->
              <div class="list-group-item">
                <div class="list-group-item-figure pl-0">
                  <a :href="base+'/book/view/'+item.slug" class="user-avatar user-avatar-lg">
                    <img class="" :src="base+'/'+item.photo" />
                  </a>
                </div>
                <div class="list-group-item-body">
                  <div class="d-sm-flex justify-content-sm-between align-items-sm-center">
                    <div class="team w-100 w-md-50">
                      <h4 class="list-group-item-title ellipsis">
                        <a :href="base+'/book/view/'+item.slug">{{ item.title }}</a> 
                        <!-- <a v-if="item.admin_id == auth_id" :href="base+'/book/view/'+item.slug" class="mention ml-1" data-toggle="tooltip" :title="lang.my"> <i class="fa fa-circle"></i> </a> -->
                      </h4>
                      <p class="list-group-item-text ellipsis">
                        {{ item.writer }}
                        <br />
                        <small>{{ diffForHumans(item.created_at) }}</small>
                      </p>
                    </div>
                    <ul class="list-inline text-muted mb-0">
                      <li class="list-inline-item mr-3" data-toggle="tooltip" title="" data-placement="top" :data-original-title="lang.comment">
                        <i class="fas fa-comment text-teal"></i> {{ _n2l_int(item.comment.length) }}
                      </li>
                      <!-- <li class="list-inline-item mr-3" data-toggle="tooltip" title="" data-placement="top" :data-original-title="item.admin.name">
                        <i class="fas fa-user-tie text-pink"></i> 
                      </li> -->
                      <li class="list-inline-item mr-3" data-toggle="tooltip" title="" data-placement="top" :data-original-title="lang.price">
                        <i class="fa fa-coins text-yellow"></i> {{ _n2l_int(item.cost) }} 
                      </li>
                      <!-- <li class="list-inline-item mr-3" data-toggle="tooltip" title="" data-placement="top" :data-original-title="item.created_at_human">
                        <i class="fa fa-clock text-info"></i>
                      </li> -->
                    </ul>
                  </div>
                </div>
                <div v-if="item.admin_id == auth_id" class="list-group-item-figure pr-0">
                  <div class="dropdown">
                    <span @click.prevent class="btn btn-sm btn-icon btn-secondary rounded-right" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                      <i class="fad fa-ellipsis-h"></i>
                    </span>
                    <!-- .dropdown-menu -->
                    <div class="dropdown-menu dropdown-menu-right stop-propagation p-1 text-center" style="min-width: 1rem;">
                      <div class="dropdown-arrow"></div>
                      <div class="btn-group">
                        <!-- <a class="mb-0 btn btn-xs btn-light float-right rounded-0">
                          <span style="color: red;">{{ lang.delete }}</span>
                        </a> -->
                        <a :href="base+'/book/view/'+item.slug" class="mb-0 btn btn-xs btn-light float-right rounded-0">
                          <span class="text-primary">{{ lang.view }}</span>
                        </a>
                      </div>
                    </div><!-- /.dropdown-menu -->
                  </div>
                </div>
                <div v-else class="list-group-item-figure pr-0">
                  <div class="dropdown">
                    <span @click.prevent class="btn btn-sm btn-icon btn-secondary rounded-left" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                      <i class="fad fa-ellipsis-h"></i>
                    </span>
                    <!-- .dropdown-menu -->
                    <div class="dropdown-menu dropdown-menu-right stop-propagation p-1 text-center" style="min-width: 1rem;">
                      <div class="dropdown-arrow"></div>
                      <div class="btn-group">
                        <a @click.prevent="reportConfirm(url_report, item.id)" class="mb-0 btn btn-xs btn-light float-right rounded-0">
                          <span class="text-orange">{{ lang.report }}</span>
                        </a>
                        <a :href="base+'/book/view/'+item.slug" class="mb-0 btn btn-xs btn-light float-right rounded-0">
                          <span class="text-primary">{{ lang.view }}</span>
                        </a>
                      </div>
                    </div><!-- /.dropdown-menu -->
                  </div>
                </div>
              </div><!-- /.list-group-item -->
            </div><!-- /.list-group -->
          </div>

          <div v-else-if="search_result.data.length > 0 && val_filter == 'wishes' " class="card mb-0 card-fluid box-shadow-none border-0">
            <!-- .list-group -->
            <div v-for="(item, index) in search_result.data" class="list-group list-group-flush list-group-divider">
              <!-- .list-group-item -->
              <div class="list-group-item">
                <div class="list-group-item-figure pl-0">
                  <a href="#" @click.prevent="wish_detail(item, index)" class="user-avatar user-avatar-lg">
                    <img class="" :src="base+'/'+item.admin.photo" />
                  </a>
                </div>
                <div class="list-group-item-body">
                  <div class="d-sm-flex justify-content-sm-between align-items-sm-center">
                    <div class="team w-100 w-md-50">
                      <h4 class="list-group-item-title ellipsis">
                        <a href="#" @click.prevent="wish_detail(item, index)" >{{ item.title }}</a> 
                        <!-- <a v-if="item.admin_id == auth_id" href="#" class="mention ml-1" data-toggle="tooltip" :title="lang.my"> <i class="fa fa-circle"></i> </a> -->
                      </h4>
                      <p class="list-group-item-text ellipsis">
                        {{ item.writer }}
                        <br />
                        <small>{{ diffForHumans(item.created_at) }}</small>
                      </p>
                    </div>
                    <ul class="list-inline text-muted mb-0">
                      <!--  <li class="list-inline-item mr-3" data-toggle="tooltip" title="" data-placement="top" :data-original-title="lang.comment">
                        <i class="fas fa-comment text-teal"></i> {{ _n2l_int(item.comment.length) }}
                      </li> -->
                      <!-- <li class="list-inline-item mr-3" data-toggle="tooltip" title="" data-placement="top" :data-original-title="item.admin.name">
                        <i class="fas fa-user-tie text-pink"></i> 
                      </li> -->
                      <li class="list-inline-item mr-3" data-toggle="tooltip" title="" data-placement="top" :data-original-title="lang.price">
                        <i class="fa fa-coins text-yellow"></i> {{ _n2l_int(item.cost) }} 
                      </li>
                      <!-- <li class="list-inline-item mr-3" data-toggle="tooltip" title="" data-placement="top" :data-original-title="item.created_at_human">
                        <i class="fa fa-clock text-info"></i>
                      </li> -->
                    </ul>
                  </div>
                </div>
                <div v-if="item.admin_id == auth_id" class="list-group-item-figure pr-0">
                  <div class="dropdown">
                    <span @click.prevent class="btn btn-sm btn-icon btn-secondary rounded-right" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                      <i class="fad fa-ellipsis-h"></i>
                    </span>
                    <!-- .dropdown-menu -->
                    <div class="dropdown-menu dropdown-menu-right stop-propagation p-1 text-center" style="min-width: 1rem;">
                      <div class="dropdown-arrow"></div>
                      <div class="btn-group">
                        <!-- <a class="mb-0 btn btn-xs btn-light float-right rounded-0">
                          <span style="color: red;">{{ lang.delete }}</span>
                        </a> -->
                        <a href="#" @click.prevent="wish_detail(item, index)" class="mb-0 btn btn-xs btn-light float-right rounded-0">
                          <span class="text-primary">{{ lang.view }}</span>
                        </a>
                      </div>
                    </div><!-- /.dropdown-menu -->
                  </div>
                </div>
                <div v-else class="list-group-item-figure pr-0">
                  <div class="dropdown">
                    <span @click.prevent class="btn btn-sm btn-icon btn-secondary rounded-left" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                      <i class="fad fa-ellipsis-h"></i>
                    </span>
                    <!-- .dropdown-menu -->
                    <div class="dropdown-menu dropdown-menu-right stop-propagation p-1 text-center" style="min-width: 1rem;">
                      <div class="dropdown-arrow"></div>
                      <div class="btn-group">
                        <a @click.prevent="reportConfirm(url_report, item.id)" class="mb-0 btn btn-xs btn-light float-right rounded-0">
                          <span class="text-orange">{{ lang.report }}</span>
                        </a>
                        <a href="#" @click.prevent="wish_detail(item, index)" class="mb-0 btn btn-xs btn-light float-right rounded-0">
                          <span class="text-primary">{{ lang.view }}</span>
                        </a>
                      </div>
                    </div><!-- /.dropdown-menu -->
                  </div>
                </div>
              </div><!-- /.list-group-item -->
            </div><!-- /.list-group -->
          </div>

          <div v-else class="card mb-0 card-fluid box-shadow-none border-0">
            <div class="list-group list-group-flush list-group-divider">
              <!-- .list-group-item -->
              <div class="list-group-item">
                <div class="list-group-item-body px-0">
                  <div class="d-sm-flex justify-content-sm-between align-items-sm-center">
                    <div class="team w-100">
                      <div class="alert alert-warning has-icon mb-0" role="alert">
                        <div class="alert-icon">
                          <span class="oi oi-info"></span>
                        </div>
                        <span v-if="query.length == 0 && new_query.length == 0" class="d-block mt-1"><strong>{{ lang.search_box_empty }}</strong></span>
                        <span v-else-if="new_query.length != 0 && !notification_id" class="d-block mt-1"><strong>{{ lang.press_enter_to_search }}</strong></span>
                        <span v-else class="d-block mt-1">{{ lang.no_result_found_for_key }} <strong>{{ locale == 'en' ? new_query:'' }}</strong></span>
                      </div>
                    </div>
                  </div>
                </div>
              </div><!-- /.list-group-item -->
            </div>
          </div>





          <!-- .pagination -->
          <nav v-if="search_result.data.length > 0 && pagination.last_page != 1" aria-label="...">
            <ul class="pagination pagination-sm justify-content-center">
              <li class="page-item cursor-pointer" :class="{ disabled: pagination.current_page <= 1 }">
                <a class="page-link" @click.prevent="changePage(pagination.current_page - 1); body_search(val_type, pagination.current_page)">{{ lang.previous }}</a>
              </li>
              <li class="page-item cursor-pointer" :class="isCurrentPage(1) ? 'active' : ''">
                <a class="page-link" @click.prevent="changePage(1); body_search(val_type, 1)"  >{{ _n2l('1') }}
                  <span v-if="isCurrentPage(1)" class="sr-only">(current)</span>
                </a>
              </li>


              <li v-if="leftDots_" class="page-item cursor-default disabled">
                <a class="page-link" @click.prevent="">...</a>
              </li>
              <li class="page-item cursor-pointer" v-for="page in pages" :key="page" :class="isCurrentPage(page) ? 'active' : ''">
                <a class="page-link" @click.prevent="changePage(page); body_search(val_type, page)">{{ _n2l(page) }}
                  <span v-if="isCurrentPage(page)" class="sr-only">(current)</span>
                </a>
              </li>
              <li v-if="rightDots_" class="page-item cursor-default disabled">
                <a class="page-link" @click.prevent="">...</a>
              </li>


              <li class="page-item cursor-pointer" :class="isCurrentPage(pagination.last_page) ? 'active' : ''">
                <a class="page-link" @click.prevent="changePage(pagination.last_page); body_search(val_type, pagination.last_page)">{{ _n2l(pagination.last_page) }}
                  <span v-if="isCurrentPage(pagination.last_page)" class="sr-only">(current)</span>
                </a>
              </li>
              <li class="page-item cursor-pointer" :class="{ disabled: pagination.current_page >= pagination.last_page }">
                <a class="page-link" @click.prevent="changePage(pagination.current_page + 1); body_search(val_type, pagination.current_page)">{{ lang.next }}</a>
              </li>
            </ul>
          </nav><!-- /.pagination -->

        </div><!-- /.card-body -->
      </div><!-- /.card -->
    </form>
  </div>
</template>
<script>
  import { store } from '../../store'
  export default {
    name: 'SearchCard',
    props: ['filter', 'type', 'page', 'query', 'action', 'url_report', 'result'],
    data() {
      return {
        val_filter: (this.filter == '' || this.filter == 'book') ? 'books' : this.filter,
        val_type: (this.type == '' || this.type == 'all') ? 'all' : this.type,
        val_page: this.page,
        search: '',
        base: '',
        body_search_url: '',
        show_class: '',
        locale: '',
        notification_id: false,
        lang: {},
        new_url: '',
        fa_spin: '',
        search_result: JSON.parse(this.result),
        new_query: this.query,
        auth_id: 0,

        pagination: JSON.parse(this.result),
        offset: 3,
        leftDots_: false,
        rightDots_: false
      }
    },
    mounted() {
      
      this.isCurrentPage(parseInt(this.page));
      this.changePage(parseInt(this.page));

      if (this.new_query.length > 0) this.notification_id = true;

      this.base = store.state.my_url;
      this.auth_id = decode_base64(store.state.auth_id);
      this.locale = store.state.locale;
      this.body_search_url = store.state.my_url+'/search';
      this.lang = store.state.file_default;
    },
    methods: {
      wish_detail: function (thisWish, index) {
        let writers = thisWish.writer.split(';');
        let writer_html = '';
        let description = thisWish.description ? thisWish.description:'';

        writers.forEach( function(element, index) {
          writer_html += `<footer><cite title="Author">${element}</cite></footer>`;
        });

        let wish_body = `<div class="card card-expansion-item rounded-0 box-shadow-none expanded">`+
                          `<div id="collapse0" aria-labelledby="heading0" data-parent="#accordion2" class="collapse show" style="">`+
                            `<div class="card-body pt-1 px-0 pb-2">`+
                              `<ul class="list-group pb-2">`+
                                `<li class="list-group-item px-2 px-lg-2 pb-1 border-bottom bg-light rounded-0">`+
                                  `<a href="#" role="button" class="btn-account">`+
                                    `<div class="user-avatar user-avatar-lg">`+
                                      `<img src="${this.base}/${thisWish.admin.photo}" alt="">`+
                                    `</div>`+
                                    `<div class="account-summary">`+
                                      `<p class="account-name"> ${thisWish.admin.name} </p>`+
                                      `<p class="account-description"> ${diffForHumans(thisWish.created_at)} </p>`+
                                    `</div>`+
                                  `</a>`+
                                `</li>`+
                                `<li class="list-group-item px-2 px-lg-2 py-0 pt-2">`+
                                  `<span class="col-4 col-lg-3 px-0 font-weight-bold"> Grade<span class="float-right pr-2">: </span></span>`+
                                  `<span class="col-8 col-lg-9 px-0">`+
                                    `<span class="d-inline-block">${thisWish.grade.title}</span>`+
                                  `</span>`+
                                `</li>`+
                                `<li class="list-group-item px-2 px-lg-2 py-0">`+
                                  `<span class="col-4 col-lg-3 px-0 font-weight-bold"> Edition<span class="float-right pr-2">: </span></span>`+
                                  `<span class="col-8 col-lg-9 px-0"><span class="d-inline-block">${thisWish.edition}</span></span>`+
                                `</li>`+
                                `<li class="list-group-item px-2 px-lg-2 py-0">`+
                                  `<span class="col-4 col-lg-3 px-0 font-weight-bold"> Contact<span class="float-right pr-2">: </span></span>`+
                                  `<span class="col-8 col-lg-9 px-0">`+
                                    `<span class="d-inline-block">${thisWish.district.name}, ${thisWish.upazila.name}, ${thisWish.location}.<br>${thisWish.contact}</span>`+
                                  `</span>`+
                                `</li>`+
                                `<li class="list-group-item px-2 px-lg-2 py-0">`+
                                  `<span class="col-4 col-lg-3 px-0 font-weight-bold"> Description<span class="float-right pr-2">: </span></span>`+
                                  `<span class="col-8 col-lg-9 px-0">`+
                                    `<span class="d-inline-block">${description}</span>`+
                                  `</span>`+
                                `</li>`+
                              `</ul>`+
                            `</div>`+
                          `</div>`+
                        `</div>`;
        let wishTitle = `<div id="heading0" class="card-header border-0 p-0">`+
                          `<div>`+
                            `<span>`+
                              `<blockquote class="text-left h5 m-0"><p class="mb-0"> ${thisWish.title} </p> ${writer_html}</blockquote>`+
                            `</span>`+
                          `</div>`+
                        `</div>`;

        const bvHtml = this.$createElement;
        const message = bvHtml('div', { domProps: { innerHTML: wish_body } });
        const title = bvHtml('div', { domProps: { innerHTML: wishTitle } });

        this.$bvModal.msgBoxOk([message], {
          title: [title],
          // size: 'sm',
          buttonSize: 'sm',
          okVariant: 'success',
          modalClass: 'stop-propagation',
          okTitle: this.locale.ok,
          // cancelTitle: this.locale.no,
          bodyClass: 'mb-3',
          headerClass: 'pb-1',
          footerClass: 'd-none p-2',
          hideHeaderClose: false,
          centered: true
        })
        .then(value => {
          // console.log(value);
        })
        .catch(err => {
          console.log(err);
        });
      },
      
      diffForHumans: (date) => {
        return diffForHumans(date);
      },

      reportConfirm: (url, base64_id) => { reportConfirm(url, encode_base64(base64_id)); },

      clicked: function (_type) {
        let search_query = encodeURIComponent(this.new_query).replace(/%20/gm, '+');
        this.new_url = this.base+'/search?q='+search_query+'&t='+_type+'&f='+this.val_filter;
        window.history.pushState({}, null, this.new_url);
      },
      getDate: function (val) {
        let date = new Date(val);
        date = date.getDate() + '/' + (date.getMonth()+1) + '/' + date.getFullYear();

        return date;
      },
      _n2l: function (str) {
        return n2l(str.toString());
      },
      _n2l_int: function (str) {
        return isNaN(parseInt(str)) ? n2l('0'):n2l(parseInt(str).toString());
      },

      val_filter_set: function (val) {
        this.val_filter = val;
      },
      val_type_set: function (val) {
        this.val_type = val;
      },

      body_search: function (_type, page=0) {
        if (this.new_query.length > 0) {

          this.fa_spin = 'fa-pulse';
          let search_query = encodeURIComponent(this.new_query).replace(/%20/gm, '+');
          let url_params = '?q='+search_query+'&t='+_type+'&f='+this.val_filter;

          if (page > 0) url_params += '&page='+page;
          else url_params += '&page=1';

          this.new_url = this.base+'/search'+url_params;
          window.history.pushState({}, null, this.new_url);

          axios.get(this.body_search_url+'/axios/body'+url_params)
          .then((response) => {
            this.fa_spin = '';

            if (response.data.data.length > 0) {
              this.notification_id = false;
              this.search_result = response.data;
              this.pagination = response.data;
              this.changePage(this.pagination.current_page);
            } else {
              this.notification_id = true;
              this.search_result = [];
              this.search_result.data = [];
            }
          })
          .catch((exception) => {
            this.notification_id = true;
            this.fa_spin = '';
            console.log(exception);
          });

        } else {
          toastr.error(this.lang.search_box_empty);
          this.notification_id = true;
          this.fa_spin = '';
        }
      },
      isCurrentPage(page){
        return this.pagination.current_page === page
      },
      changePage(page) {

        if (page > this.pagination.last_page) {
          page = this.pagination.last_page;
        }
        this.pagination.current_page = page;


        if (this.offset >= page) this.leftDots_ = false;
        else this.leftDots_ = true;


        if (this.pagination.last_page - this.offset + 1 <= page) this.rightDots_ = false;
        else this.rightDots_ = true;

        
        if (this.pagination.last_page - 1 == this.offset) this.rightDots_ = false;

        // this.$emit('paginate');
      }
    },
    computed: {
      pages() {
        let pages = []
        let from = this.pagination.current_page - Math.floor(this.offset / 2)
        if (from < 1) {
          from = 1
        }
        let to = from + this.offset - 1

        if (to > this.pagination.last_page) {
          to = this.pagination.last_page
        }
        while (from <= to) {
          if (from > 1 && from < this.pagination.last_page) {
            pages.push(from)
          }
          from++
        }

        return pages
      }
    }
  };
</script>
<style>
  .rounded-0 .btn{border-radius: 0;}
</style>