<template>
	<div>
    <div class="card rounded-0 border-primary box-shadow">
      <!-- .card-header -->
      <div class="card-header border-0 bg-primary text-white rounded-0 py-2">
        <a class="card-title mb-0"> {{ lang.update_book_info }} </a>
      </div><!-- /.card-header -->
      <!-- .card-body -->
      <div class="card-body">

        <ValidationObserver v-slot="{ invalid }">
          <!-- form .needs-validation -->
          <form method="POST" @submit="submit($event, invalid)" class="needs-validation" enctype="multipart/form-data">

            <!-- error message -->
            <div v-if="this_errors" class="alert alert-danger px-0 alert-dismissible fade show" role="alert">
              <ul class="list-group border-0 box-shadow-none">
                <li class="list-group-item bg-transparent" v-for="error in this_errors">{{ _n2l(error) }}</li>
              </ul>
              <button type="button" class="close" data-dismiss="alert" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div><!-- /error message -->


            <slot></slot>
            <!-- .form-row -->
            <div class="form-row">
              <!-- form grid -->
              <div class="col-md-6 mb-3">
                <label for="validationTitle">{{ lang.title }} <abbr :title="lang.required">*</abbr></label>
                <ValidationProvider rules="required|min:4" v-slot="{ errors }">
                  <input type="text" v-model="model_title" :class="errors.length > 0 ? 'is-invalid':''" name="title" class="form-control" id="validationTitle" :placeholder="lang.book_title" required="" />
                  <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                </ValidationProvider>
              </div><!-- /form grid -->
              <!-- form grid -->
              <div class="col-md-6 mb-3">
                <div class="form-row">
                  <!-- form grid -->
                  <div class="col-6">
                    <label for="validationEdition">{{ lang.edition }} <abbr :title="lang.required">*</abbr></label>
                    <ValidationProvider rules="required" v-slot="{ errors }">
                      <input type="text" v-model="model_edition" :class="errors.length > 0 ? 'is-invalid':''" name="edition" class="form-control" id="validationEdition" :placeholder="lang.book_edition" required="">
                      <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div><!-- /form grid -->
                  <!-- form grid -->
                  <div class="col-6">
                    <label for="validationGrade">{{ lang.grade }} <abbr :title="lang.required">*</abbr></label>
                    <ValidationProvider rules="required" v-slot="{ errors }">
                      <select name="grade_id" v-model="model_grade_id" :class="errors.length > 0 ? 'is-invalid':''" class="custom-select d-block w-100" id="validationGrade" required="">
                      	<option disabled="" selected=""> {{ lang.choose }}... </option>
                      	<option v-for="grade in all_grades" :value="grade.id"> {{ locale == 'bn' ? grade.title_bn : grade.title }} </option>
                      </select>
                      <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div><!-- /form grid -->
                </div><!-- /form grid -->
              </div><!-- /form grid -->
              <!-- form grid -->
            </div><!-- /.form-row -->
            <div class="form-row">
              <div class="col-md-6 mb-3">
                <label for="validationAuthor">{{ lang.author }} <abbr :title="lang.required">*</abbr></label>
                <ValidationProvider rules="required|min:4" v-slot="{ errors }">
                  <input type="text" v-model="model_writer" :class="errors.length > 0 ? 'is-invalid':''" name="writer" class="form-control" id="validationAuthor" :placeholder="place_author" aria-describedby="inputGroupPrepend" required="">
                  <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                </ValidationProvider>
              </div><!-- /form grid -->
              <!-- form grid -->
              <div class="col-md-6 mb-3">
                <div class="form-group mb-0">
                  <label for="validationPhoto">{{ lang.photo }} <abbr class="d-none" :title="lang.required">*</abbr><code class="text-muted">[{{ lang.can_not_update }}]</code></label>
                  <div class="custom-file">
                    <input type="file" name="photo" disabled="disabled" class="custom-file-input disabled" id="validationPhoto" aria-describedby="inputGroupPrepend" @click.prevent="" /> <label class="custom-file-label" for="validationPhoto">{{ lang.choose_file }}</label>
                  </div>
                </div>
              </div><!-- /form grid -->
            </div><!-- /.form-row -->
            <!-- .form-row -->
            <div class="form-row">
              <!-- grid column -->
              <div class="col-md-6 mb-3">
                <label for="validationDistrict">{{ lang.district }} <abbr :title="lang.required">*</abbr></label>
                <ValidationProvider rules="required" v-slot="{ errors }">
                  <select @change="axiosUpazila" name="district_id" v-model="model_district_id" :class="errors.length > 0 ? 'is-invalid':''" class="custom-select d-block w-100" id="validationDistrict" required="">
                    <option disabled="" selected=""> {{ lang.choose }}... </option>
                    <option v-for="district in all_districts" :value="district.id"> {{ locale == 'bn' ? district.bn_name : district.name }} </option>
                  </select>
                  <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                </ValidationProvider>
              </div><!-- /grid column -->
              <!-- grid column -->
              <div class="col-md-6 mb-3">
                <label class="d-block" for="validationUpazila">{{ lang.upazila }} <abbr :title="lang.required">*</abbr><i v-if="refreshSpin == 'fa-spin'" class="float-right pt-1 fa fa-sync-alt" :class="refreshSpin"></i></label>
                <ValidationProvider rules="required" v-slot="{ errors }">
                  <select name="upazila_id" v-model="model_upazila_id" :class="errors.length > 0 ? 'is-invalid':''" class="custom-select d-block w-100" id="validationUpazila" required="">
                    <option disabled="" selected=""> {{ lang.choose }}... </option>
                    <option v-for="upazila in all_upazilas" :value="upazila.id"> {{ locale == 'bn' ? upazila.bn_name : upazila.name }} </option>
                  </select>
                  <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                </ValidationProvider>
              </div><!-- /form grid -->
            </div><!-- /.form-row -->
            <!-- .form-row -->
            <div class="form-row">
              <!-- grid column -->
              <div class="col-md-6">
                <div class="form-row">
                  <div class="col-12 mb-3">
                		<div class="form-row">
                       <!-- form grid -->
                			<div class="col-6">
                      	<label for="validationCondition">{{ lang.condition }} <abbr :title="lang.required">*</abbr></label>
                        <ValidationProvider rules="required" v-slot="{ errors }">
                        	<select name="book_condition" v-model="model_book_condition" :class="errors.length > 0 ? 'is-invalid':''" class="custom-select d-block w-100" id="validationCondition" required="">
                        		<option disabled="" selected=""> {{ lang.choose }}... </option>
                        		<option value="3"> {{ lang.new }} </option>
                        		<option value="2"> {{ lang.good }} </option>
                        		<option value="1"> {{ lang.old }} </option>
                        	</select>
                          <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                        </ValidationProvider>
                       </div><!-- /form grid -->
                			<div class="col-6">
                        <label for="validationCost">{{ lang.price }} <abbr :title="lang.required">*</abbr></label>
                        <ValidationProvider rules="required|positive" v-slot="{ errors }">
                          <input type="number" v-model="model_price" :class="errors.length > 0 ? 'is-invalid':''" name="cost" class="form-control" id="validationCost" :placeholder="lang.book_price" aria-describedby="inputGroupPrepend"  step="1" min="0" required="">
                          <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                        </ValidationProvider>
                  		</div>
                  	</div>
                  </div>
                  <div class="col-12 mb-3">
                    <label for="validationMobile">{{ lang.mobile }} <abbr :title="lang.required">*</abbr></label>
                    <ValidationProvider rules="required|min:11" v-slot="{ errors }">
                      <input type="text" name="contact" v-model="model_contact" :class="errors.length > 0 ? 'is-invalid':''" class="form-control" id="validationMobile" :placeholder="lang.mobile_no" aria-describedby="inputGroupPrepend" required="" />
                      <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
              </div><!-- /form grid -->
              <!-- grid column -->
              <div class="col-md-6 mb-3">
                <label for="validationAddress">{{ lang.address }}  <abbr :title="lang.required">*</abbr></label>
                <ValidationProvider rules="required|min:4" v-slot="{ errors }">
                  <textarea name="location" v-model="model_address" :class="errors.length > 0 ? 'is-invalid':''" class="form-control" id="validationAddress" rows="3" :placeholder="lang.present_address" aria-describedby="inputGroupPrepend" required="" style="height: 121px;"></textarea>
                  <span class="d-block mt-1 val-error text-warning m-0">{{ errors[0] }}</span>
                </ValidationProvider>
              </div><!-- /grid column -->
            </div><!-- /.form-row -->

            <!-- .form-actions -->
            <div class="form-actions">
              <button class="btn btn-primary" type="submit" :disabled="invalid">{{ lang.update }}</button>
            </div><!-- /.form-actions -->
          </form><!-- /form .needs-validation -->
        </ValidationObserver>
      </div><!-- /.card-body -->
    </div>
	</div>
</template>
<script>
	import { store } from '../../store';

	export default {
		name: 'AddBook',
		props: ['grades', 'districts', 'upazilas', 'upazila_url', 'admin', 'book', 'old_inputs', 'form_errors'],
		data(){
			return {
				all_grades		: JSON.parse(this.grades),
				all_districts	: JSON.parse(this.districts),
				all_upazilas	: JSON.parse(this.upazilas),
				this_admin		: JSON.parse(this.admin),
        this_book     : JSON.parse(this.book),
        this_errors   : JSON.parse(this.form_errors),

				model_contact         : JSON.parse(this.admin).mobile,
				model_grade_id		    : '',
				model_district_id	    : '',
				model_upazila_id      : '',
        model_book_condition  : '',
        model_title           : '',
        model_edition         : '',
        model_writer          : '',
        model_price           : '',
        model_address         : '',

				lang          : {},
				locale	 		  : '',
				auth_id			  : 0,
				base_url		  : '',
				refreshSpin		: '',
				place_author	: ''
			}
		},
		mounted(){

      if(parseInt(this.old_inputs)) {
        let this_model = this;
        Object.keys(localStorage).forEach( function(element, index) {
          if (_.startsWith(element, 'model_')) {
            this_model[element] = localStorage.getItem(element);
          }
        });
      } else {
        this.model_book_condition = this.this_book.book_condition;
        this.model_grade_id       = this.this_book.grade_id;
        this.model_district_id    = this.this_book.district_id;
        this.model_upazila_id     = this.this_book.upazila_id;

        this.model_title          = this.this_book.title;
        this.model_edition        = this.this_book.edition;
        this.model_writer         = this.this_book.writer;
        this.model_price          = this.this_book.cost;
        this.model_address        = this.this_book.location;
      }
      localStorage.clear();

			this.locale 		     = store.state.locale;
			this.base_url 		   = store.state.my_url;
			this.admin_id        = store.state.admin_id;
			this.lang            = store.state.file_default;
			this.lang.author_    = { _1: store.state.file_default.author_1, _2: store.state.file_default.author_2, _3: store.state.file_default.author_3};
			this.place_author    = this.lang.author_._1 + '; '+ this.lang.author_._2 + '; '+ this.lang.author_._3 + '; ...';


		},
		methods: {
			diffForHumans: (date) => {
				return diffForHumans(date);
			},
      _n2l: function (str) {
        return _.upperFirst(n2l(str.toString()));
      },
      check_validation: function (error, model) {
        if (error.length>0 && model.length==0) return 'is-invalid'
        else return 'is-valid'
      },
      submit: function(e, is_invalid) {
        if (is_invalid) {
          e.preventDefault();
        }
      },
			axiosUpazila: function () {
				if (this.model_district_id > 0) {
					this.refreshSpin = 'fa-spin';

					axios.get(this.upazila_url+'/'+this.model_district_id)
					.then((response) => {
						this.all_upazilas = response.data;
						this.refreshSpin = '';
					})
					.catch((exception) => {
						this.refreshSpin = '';
					    console.log(exception);
					});

				} else {
				}
			},
			axiosPostComment: function () {
				this.loading = 1;
				let data = {
					book_id: this.book_id,
					comment_body: this.comment_body
				};
				if (this.comment_body.length > 0) {
					axios.post(this.post_url, data)
					.then((response) => {
						this.all_comments  = response.data;
						this.comment_body = '';
						this.loading = 0;
						// toastr.success("Comment");
					})
					.catch((exception) => {
						this.loading = 0;
						toastr.error("Something went worng!");
					    console.log(exception);
					});
				} else {
					toastr.warning("Write something in the text area");
					this.loading = 0;
				}
			}
		},
    created() {},
    computed: {},
    watch: {
      model_title(newVal) { localStorage.model_title = this.model_title; },
      model_edition(newVal) { localStorage.model_edition = this.model_edition; },
      model_grade_id(newVal) { localStorage.model_grade_id = this.model_grade_id; },
      model_writer(newVal) { localStorage.model_writer = this.model_writer; },
      model_district_id(newVal) { localStorage.model_district_id = this.model_district_id; },
      model_upazila_id(newVal) { localStorage.model_upazila_id = this.model_upazila_id; },
      model_book_condition(newVal) { localStorage.model_book_condition = this.model_book_condition; },
      model_price(newVal) { localStorage.model_price = this.model_price; },
      model_contact(newVal) { localStorage.model_contact = this.model_contact; },
      model_address(newVal) { localStorage.model_address = this.model_address; }
    }
	};
</script>
<style>
	.rounded-0 {border-radius: 0 !important;}
</style>