<template>
	<a href="#" @click.prevent="trash" :class="classes">{{ title_ }}</a>
</template>
<script>
	import { store } from '../../store'
	export default {
		name: 'MessageReadAll',
		props: ['messageCount', 'hasNotified', 'allMessages', 'thisMessage', 'currentMessage', 'url_', 'loading_', 'classes', 'title_'],
		data(){
			return {
				base: '',
				local: '',
				// messages: {},
			}
		},
		mounted(){
			this.base = store.state.my_url;
			this.locale = store.state.file_default;
		},
		methods: {
			trash: function () {
				this.$emit('update:loading_', true);

				let currentMessageId = this.currentMessage.id;
				let messages = {};

				axios.post(this.url_, {
					base64_id: encode_base64(this.thisMessage.id)
				}).then((response) => {
					if (response.data != 0) {
						this.$emit('update:loading_', false);

						// this.$emit('update:thisMessage', response.data[0].data[0]);
						messages = response.data[0].data; 

						if (this.thisMessage.id == this.currentMessage.id) {
							this.$emit('update:currentMessage', '');
						} else {
							Object.keys(messages).forEach( function(element, ind) {
								if(messages[element].id == currentMessageId) {
									messages[element].active = true;
								}
							});
						}

						this.$emit('update:allMessages', messages);

						this.$emit('update:messageCount', 0);
						if (parseInt(response.data[1]) > 0) {
							this.$emit('update:hasNotified', 'has-notified');
						}

				  		toastr.success(this.locale.success+'!');
					}
					else {
				  		toastr.error(this.locale.something_went_wrong+'!');
					}

				}).catch((error) => {
					this.$emit('update:loading_', false);
					console.error(error);
				  	toastr.error(this.locale.something_went_wrong+'!');
				});
			}
		}
	};
</script>