<template>
	<div>
		<div v-if="is_view" class="mt-2" :class="{small: locale == 'bn'}">
			<a v-if="this_admin_id == admin_id" href="" @click.prevent="deleteConfirm(url_delete, base64_id, redirect)" class="cursor-pointer text-muted text-danger-h mr-2 text-lowercase" :title="lang.delete">{{ lang.delete }}</a>
			<a v-if="this_admin_id == admin_id" :href="base+'/profile/edit-book/'+slug" class="cursor-pointer text-muted text-teal-h mr-2 text-lowercase" :title="lang.edit">{{ lang.edit }}</a>
			<a v-if="this_admin_id == admin_id" :href="base+'/profile/bell/'+slug" class="cursor-pointer text-muted text-teal-h mr-2 text-lowercase" :title="lang.bell_list_people">{{ lang.bells }}</a>

			<a v-if="this_admin_id != admin_id" href="" @click.prevent="reportConfirm(url_report, base64_id)" class="cursor-pointer text-muted text-warning-h mr-2 text-lowercase" :title="lang.report">{{ lang.report }}</a>
			<a v-if="this_admin_id != admin_id" href="" @click.prevent="bellConfirm(url_bell, base64_id, is_view)" class="cursor-pointer text-muted text-primary-h mr-2 text-lowercase rounded font-weight-bold" :title="lang.bell_the_book">{{ lang.bell }}</a>

			<a :href="base+'/profile/my-books'" class="cursor-pointer text-muted text-primary-h float-right text-lowercase" :title="lang.my_book_list">{{ lang.my_books }}</a>
		</div>
		<div v-else>
			<a v-if="this_admin_id != admin_id" @click.prevent="bellConfirm(url_bell, base64_id, is_view)" class="btn btn-block btn-sm btn-primary"><i class="oi oi-bell text-white"></i></a>
			<a v-else @click.prevent="myBook(lang.cant_ask_own)" class="btn btn-block btn-sm btn-warning"><i class="oi oi-bell text-muted"></i></a>
		</div>
	</div>
</template>
<script>
	import { store } from '../../store';

	export default {
		name: 'ViewAction',
		props: ['url_delete', 'url_report', 'url_bell', 'url_bell_list', 'slug', 'redirect', 'base64_id', 'admin_id', 'view'],
		data(){
			return {
				base: '',
				lang: '',
				locale: '',
				this_admin_id: '',
				is_view: true,
			}
		},
		mounted(){
			this.is_view = this.view == '1' ? true:false;
			this.this_admin_id = decode_base64(store.state.auth_id);
			this.locale = store.state.locale;
			this.base = store.state.my_url;
			this.lang = store.state.file_default;
			// console.log(this.this_admin_id, this.admin_id, this.this_admin_id, this.admin_id);
		},
		methods: {

			bellConfirm: (url, base64_id, is_view) => { bellConfirm(url, base64_id, is_view); },
			reportConfirm: (url, base64_id) => { reportConfirm(url, base64_id); },
			deleteConfirm: (url, base64_id, redirect) => { deleteConfirm(url, base64_id, redirect); },

			myBook: (cant_ask_own) => { toastr.warning(cant_ask_own+'!'); },

			axiosDeleteComment: function (id) {
				if (this.delete_comment.id == id) {
					this.refreshSpin = 'fa-spin';

					axios.get(this.post_url+'/'+id)
					.then((response) => {
						this.all_comments  = response.data;
						toastr.success(this.lang.comment_deleted_successfully+"!");
						this.delete_comment = {};
						this.refreshSpin = '';
					})
					.catch((exception) => {
						this.refreshSpin = '';
						toastr.error(this.lang.something_went_wrong);
					    console.log(exception);
					});

				} else {
					this.delete_comment.id = id;
					this.delete_comment.count = 1;
					Toast.fire({
					  icon: 'warning',
					  title: '<span class="m-0 pl-2">'+this.lang.click_again+'...</span>'
					});
				}
			},
		},
		created(){

		}
	};
</script>