<template>
	<div>
    <!-- form -->
    <form class="auth-form need-validation" @submit="submit($event, check_submit())" method="POST" :action="url_register" enctype="multipart/form-data">

      <!-- error message -->
      <div v-if="this_errors" class="alert alert-danger px-0 alert-dismissible fade show" role="alert">
        <ul class="list-group border-0 box-shadow-none">
          <li class="list-group-item bg-transparent" v-for="error in this_errors">{{ _n2l(error) }}</li>
        </ul>
        <button type="button" class="close" data-dismiss="alert" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div><!-- /error message -->

      <slot></slot>
      <!-- .form-group -->
      <div class="form-group">
        <div class="form-label-group">
          <input type="text" id="inputName" @blur="validate_name(true)" v-model='model_name' name='name' class="form-control" :class="validate_name() ? 'border-danger':''" placeholder="Full Name" required="" autofocus="" autocomplete="off"> <label for="inputName">Name</label>
          <div v-if="validate_name()" class="text-danger">Required min length is 4</div>
        </div>
      </div><!-- /.form-group -->
      <!-- .form-group -->
      <div class="form-group">
        <div class="form-label-group has-spinner">
          <div class="spinner-border spinner-border-sm text-muted" :class="{'show': loading_email}" role="status">
            <span class="sr-only">Loading...</span>
          </div>
          <input type="email" id="inputEmail" @blur="check_new_user_data('email', model_email)" v-model='model_email' name='email' class="form-control" :class="error_exist_email ? 'border-danger':''" placeholder="Email" required=""> <label for="inputEmail">Email</label>
          <div v-if="validate_email()" class="text-danger"> <strong>{{ model_email }}</strong> is not a valid email</div>
          <div v-if="error_exist_email" class="text-danger">Email already exists!</div>
        </div>
      </div><!-- /.form-group -->
      <!-- .form-group -->
      <div class="form-group">
        <div class="form-label-group has-spinner">
          <div class="spinner-border spinner-border-sm text-muted" :class="{'show': loading_username}" role="status">
            <span class="sr-only">Loading...</span>
          </div>
          <input type="text" id="inputUser" @blur="check_new_user_data('username', model_username)" v-model='model_username' name='username' class="form-control" :class="validate_username(true) ? 'border-danger':''" placeholder="Username" required=""> <label for="inputUser">Username</label>
          <div v-if="error_reserve_username" class="text-danger">Reserved username</div>
          <div v-if="error_exist_username" class="text-danger">Username already exists!</div>
          <div v-if="validate_username()" class="text-danger">Username min length is 4</div>
        </div>
      </div><!-- /.form-group -->
      <!-- .form-group -->
      <div class="form-group">
        <div class="form-label-group">
          <input type="password" id="inputPassword" v-model='model_password' name='password' class="form-control" :class="validate_password() ? 'border-danger':''" placeholder="Password" required=""> <label for="inputPassword">Password</label>
          <div v-if="validate_password()" class="text-danger">Password min length is 8</div>
        </div>
      </div><!-- /.form-group -->
      <!-- .form-group -->
      <div class="form-group">
        <button class="btn btn-lg btn-primary btn-block" type="submit" :disabled="check_submit()">Sign Up</button>
      </div><!-- /.form-group -->
      <!-- .form-group -->
      <div class="form-group text-center">
        <div class="custom-control custom-control-inline custom-checkbox">
          <input type="checkbox" class="custom-control-input" id="newsletter"> <label class="custom-control-label" for="newsletter">Sign me up for the newsletter</label>
        </div>
      </div><!-- /.form-group -->
      <!-- recovery links -->
      <p class="text-center text-muted mb-0"> By creating an account you agree to the <a href="#">Terms of Use</a> and <a href="#">Privacy Policy</a>. </p><!-- /recovery links -->
    </form><!-- /.auth-form -->
	</div>
</template>
<script>
	import { store } from '../../store';

	export default {
		name: 'FormRegister',
		props: ['url_register', 'url_validate', 'old_inputs', 'form_errors'],
		data(){
			return {
				model_name      : '',
				model_email     : '',
				model_username  : '',
        model_password  : '',

        this_errors     : JSON.parse(this.form_errors),

        error_reserve_username  : false,
        error_exist_email       : false,
        error_exist_username    : false,

        lang          : {},
				locale	 		  : '',
				base_url		  : '',

        reg_exp_email     : /\S+@\S+\.\S+/,
        // reg_exp_name   : /[^\w\s\_]/gi,
        reg_exp_name      : /[^a-zA-Z\.\-\_\s]/g,
        reg_exp_username  : /[^a-zA-Z0-9\_]/g,

        invalid           : true,
        loading_email     : false,
        loading_username  : false,
        disabled          : true
			}
		},
		mounted(){

      if(parseInt(this.old_inputs)) {
        let this_model = this;
        Object.keys(localStorage).forEach( function(element, index) {
          if (_.startsWith(element, 'model_')) {
            this_model[element] = localStorage.getItem(element);
          }
        });
      }
      localStorage.clear();

			this.locale 		= store.state.locale;
			this.base_url 		= store.state.my_url;
			this.admin_id 		= store.state.admin_id;
			this.lang 			= store.state.file_default;
			this.lang.author_ 	= { _1: store.state.file_default.author_1, _2: store.state.file_default.author_2, _3: store.state.file_default.author_3};
			this.place_author 	= this.lang.author_._1 + '; '+ this.lang.author_._2 + '; '+ this.lang.author_._3 + '; ...';
		},
		methods: {
      validate_name: function (blur=false) {
        if (blur) this.model_name = this.model_name.replace(this.reg_exp_name, '');
        return this.model_name.length!=0 && !(this.model_name.length>3);
      },
			validate_email: function () {
        return this.model_email.length!=0 && !this.reg_exp_email.test(String(this.model_email).toLowerCase());
      },
      validate_username: function (class_=false) {
        if (class_) return this.error_exist_username || this.error_reserve_username;
        return this.model_username.length!=0 && !(this.model_username.length>3);
      },
      validate_password: function () {
        return this.model_password.length!=0 && !(this.model_password.length>7);
      },
      check_submit: function () {
        return !(this.model_name.length>3 && this.reg_exp_email.test(String(this.model_email).toLowerCase()) && this.model_username.length>3 && this.model_password.length>7 && !this.error_reserve_username && !this.error_exist_email && !this.error_exist_username && !this.loading_email && !this.loading_username);
      },
      check_new_user_data: function (name, data) {
        if (name == 'email') {
          this.loading_email          = true;
          this.error_exist_email      = false;
        }
        else if (name == 'username') {
          this.loading_username       = true;
          this.error_reserve_username = false;
          this.error_exist_username   = false;
        }

        axios.post(this.url_validate, {
          field: name,
          data: data
        }).then((response) => {

          this.loading_email    = false;
          this.loading_username = false;

          switch (response.data) {
            case 0:
              // console.log(response.data, 'ok');
              break;
            case 1:
              this.error_reserve_username = true;
              break;
            case 2:
              if (name == 'email') this.error_exist_email       = true;
              if (name == 'username') this.error_exist_username = true;
              break;
            default:
              toastr.error(this.lang.something_went_wrong+"!");
              break;
          }

        }).catch((error) => {
          toastr.error(this.lang.something_went_wrong+"!");

          this.loading_email = false;
          this.loading_username = false;

          console.error(error);
        });

      },
      diffForHumans: (date) => {
				return diffForHumans(date);
			},
      _n2l: function (str) {
        return _.upperFirst(n2l(str.toString()));
      },
      check_validation: function (error, model) {
        if (error.length>0 && model.length==0) return 'is-invalid'
        else return 'is-valid'
      },
      submit: function(e, is_valid) {
        console.log(e, is_valid)
        if (is_valid) {
          e.preventDefault();
        }
      }
		},
		created(){

		},
    computed: {
    },
    watch: {
      model_name(newVal) { localStorage.model_name = this.model_name; },
      model_email(newVal) { localStorage.model_email = this.model_email; },
      model_username(newVal) { localStorage.model_username = this.model_username; },
      // model_password(newVal) { localStorage.model_password = this.model_password; },
    }
	};
</script>
<style>
	.rounded-0 {border-radius: 0 !important;}
</style>