<template>
  <div>
    <form class="top-bar-search d-lg-inline-block w-auto my-2 my-lg-0 px-0" :action="base+'/search?q='+search_query" method="GET">
      <div class="input-group input-group-search dropdown">
        <div class="input-group-prepend">
          <span class="input-group-text"><span class="oi oi-magnifying-glass"></span></span>
        </div>
        <input id="nav-search-input" class="form-control" ref="nav_search_input" @input="nav_search" v-model="search" name="q" type="text" :placeholder="lang_search" data-toggle="dropdown" aria-label="Search" aria-expanded="false" autocomplete="off" />
        <!-- .dropdown-menu -->
        <div v-if="show_list" class="dropdown-menu dropdown-menu-rich dropdown-menu-xl ml-n4 mw-100" style="right: 0px !important; left: unset !important;">
          <div class="dropdown-arrow ml-3"></div>
          <!-- .dropdown-scroll -->
          <div v-if="fa_spin != 'fa-spin'" class="dropdown-scroll height-unset pt-1 stop-propagation">

            <perfect-scrollbar>
              <!-- .list-group -->
              <div class="list-group list-group-flush list-group-reflow mb-2 my-2">
                <!-- .list-group-item -->
                <div v-for="result in search_result" class="list-group-item px-2 py-1">
                  <a :href="base+'/book/view/'+result.slug" class="stretched-link"><span class="sr-only">Go to search result</span></a>
                  <div class="user-avatar bg-muted my-auto">
                    <img :src="base+'/'+result.photo" alt="">
                  </div>
                  <div class="ml-2">
                    <div class="mb-n1">{{ result.title }}</div><small class="text-muted">{{ result.admin.name }}</small>
                  </div>
                </div><!-- /.list-group-item -->
              </div><!-- /.list-group -->
            </perfect-scrollbar>

          </div><!-- /.dropdown-scroll -->
          <div v-else>
            <div class="dropdown-arrow ml-3"></div>
            <!-- .dropdown-scroll -->
            <div class="dropdown-scroll perfect-scrollbar h-auto ps" style="max-height: 360px">
              <!-- .list-group -->
              <div class="list-group list-group-flush list-group-reflow mb-2 my-2">
                <!-- .list-group-item -->
                <div class="list-group-item px-2 py-1">
                  <a href="#" @click.prevent="" class="stretched-link"><span class="sr-only">Go to search result</span></a>
                  <span class="text-center text-info">Fatching...</span>
                </div><!-- /.list-group-item -->
              </div><!-- .list-group -->
            </div><!-- .dropdown-scroll -->
          </div>
          <a :href="base+'/search?q='+search_query" class="dropdown-footer">Show all results</a>
        </div>
        <div v-else class="dropdown-menu dropdown-menu-rich dropdown-menu-xl ml-n4 mw-100" style="right: 0px !important; left: unset !important;">
          <div v-if="fa_spin != 'fa-spin' && search.length > 0 && !(search_result.length > 0)" >
            <div class="dropdown-arrow ml-3"></div>
            <!-- .dropdown-scroll -->
            <div class="dropdown-scroll perfect-scrollbar h-auto ps" style="max-height: 360px">
              <!-- .list-group -->
              <div class="list-group list-group-flush list-group-reflow mb-2 my-2">
                <!-- .list-group-item -->
                <div class="list-group-item px-2 py-1">
                  <a href="#" @click.prevent="" class="stretched-link"><span class="sr-only">Go to search result</span></a>
                  <span class="text-center text-danger">Nothing to show!</span>
                </div><!-- /.list-group-item -->
              </div><!-- .list-group -->
            </div><!-- .dropdown-scroll -->
          </div>
          <div v-else-if ="fa_spin != 'fa-spin' && search.length == 0" >
            <div class="dropdown-arrow ml-3"></div>
            <!-- .dropdown-scroll -->
            <div class="dropdown-scroll perfect-scrollbar h-auto ps" style="max-height: 360px">
              <!-- .list-group -->
              <div class="list-group list-group-flush list-group-reflow mb-2 my-2">
                <!-- .list-group-item -->
                <div class="list-group-item px-2 py-1">
                  <a href="#" @click.prevent="" class="stretched-link"><span class="sr-only">Go to search result</span></a>
                  <span class="text-center text-warning">Type to search!</span>
                </div><!-- /.list-group-item -->
              </div><!-- .list-group -->
            </div><!-- .dropdown-scroll -->
          </div>
          <div v-else>
            <div class="dropdown-arrow ml-3"></div>
            <!-- .dropdown-scroll -->
            <div class="dropdown-scroll perfect-scrollbar h-auto ps" style="max-height: 360px">
              <!-- .list-group -->
              <div class="list-group list-group-flush list-group-reflow mb-2 my-2">
                <!-- .list-group-item -->
                <div class="list-group-item px-2 py-1">
                  <a href="#" @click.prevent="" class="stretched-link"><span class="sr-only">Go to search result</span></a>
                  <span class="text-center text-info">Fatching...</span>
                </div><!-- /.list-group-item -->
              </div><!-- .list-group -->
            </div><!-- .dropdown-scroll -->
          </div>
        </div><!-- /.dropdown-menu -->
      </div>
    </form>
  </div>
</template>
<script>
  import { store } from '../../store'
  import { isLoggedMixin } from '../../myMixins'
  import _ from 'lodash'

  export default {
    name: 'NavSearch',
    mixins:[isLoggedMixin],
    // props: ['slug'],
    data(){
      return {
        lang_search: '',
        search: '',
        base: '',
        nav_search_url: '',
        show_list: false,
        show_class: '',
        fa_spin: '',
        search_result: 'one',
        search_query: '',
      }
    },
    mounted(){
      this.lang_search = store.state.file_default.search;
      this.base = store.state.my_url;
      this.nav_search_url = store.state.my_url+'/search';
      // this.search_result = 'one';
    },
    watch: {
      search: function(val, oldVal) {
        // this.show_class = 'show';
        // console.log(val, oldVal, this.show_class);
      }
    },
    methods: {

      nav_search: _.debounce(function () {
        if (this.search.length > 0) {
          this.fa_spin = 'fa-spin';
          this.search_result = {};
          let query = encodeURIComponent(this.search).replace(/%20/gm, '+');
          this.search_query = query;
          let url_query = this.nav_search_url+'/axios?q='+query;

          axios.get(url_query)
          .then((response) => {
            this.fa_spin = '';
            if (response.data.length > 0) {
              this.show_list = true;
              this.search_result = response.data;

              console.log(query);

            } else {
              this.show_list = false;
              this.search_result = {};
            }
          })
          .catch((exception) => {
            this.fa_spin = '';
            this.show_list = false;
            this.search_result = {};
            console.log(exception);
          });

        } else {
          this.fa_spin = '';
          this.show_list = false;
          this.search_result = 'one';
        }
      }, 1000)
    }
  };
</script>