<template>
	<li class="nav-item dropdown header-nav-dropdown">
		<a href="#" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" class="nav-link"><span class="oi oi-grid-three-up"></span></a> 
		<div class="dropdown-menu dropdown-menu-rich dropdown-menu-right">
			<div class="dropdown-arrow"></div>
			<div class="dropdown-sheets">
				<div class="dropdown-sheet-item">
					<a :href="urls_.add_book" class="tile-wrapper" :title="locale.add_book">
						<span class="tile tile-lg bg-indigo"><i class="fad fa-books-medical"></i></span>
						<span class="tile-peek">{{ locale.book }}</span>
					</a>
				</div>
				<div class="dropdown-sheet-item">
					<a :href="urls_.setting" class="tile-wrapper" :title="locale.profile+' '+locale.setting">
						<span class="tile tile-lg bg-success"><i class="fad fa-user-cog"></i></span>
						<span class="tile-peek">{{ locale.setting }}</span>
					</a>
				</div>
				<div class="dropdown-sheet-item">
					<a :href="urls_.wish_list" class="tile-wrapper" :title="locale.wish_list">
						<span class="tile tile-lg bg-dark"><i class="fad fa-hand-holding-box"></i></span>
						<span class="tile-peek">{{ locale.wish }}</span>
					</a>
				</div>
			</div>
		</div>
	</li>
</template>
<script>
	import { store } from '../../store'
	import { isLoggedMixin } from '../../myMixins'
	export default {
		name: 'NavAppShorts',
		mixins:[isLoggedMixin],
		props: ['urls'],
		data(){
			return {
				base: '',
				urls_: JSON.parse(this.urls),
				locale: {},
			}
		},
		mounted(){
			this.base = store.state.my_url;
			this.locale = store.state.file_default;
		},
		methods: {
		}
	};
</script>